<template>
  <div class="btn-box">
    <!-- 左边 -->
    <div class="left">
      <span v-for="(item, index) in leftBtnList" :key="index">
        <g-btn
          :icon="item.icon"
          :disabled="item.disabled"
          :text="item.text"
          @callback="callback"
          :eventName="item.eventName"
          :color="item?.btnColor"
          :type="item.type"
        />
      </span>
    </div>
    <!-- 右边 -->
    <div class="right">
      <span v-for="(item, index) in rightBtnList" :key="index">
        <g-btn
          :icon="item.icon"
          :disabled="item.disabled"
          :text="item.text"
          @callback="callback"
          :eventName="item.eventName"
          :color="item?.btnColor"
          :type="item.type"
        />
      </span>
    </div>
  </div>
</template>

<script>
export default {
  name: "GBtnGroup",
  props: {
    buttonLists: {
      type: Array,
      default: () => [],
    },
  },
  computed: {
    leftBtnList() {
      return this.buttonLists.filter((item) => !item.right && item.display);
    },
    rightBtnList() {
      return this.buttonLists.filter((item) => item.right && item.display);
    },
  },
  data() {
    return {};
  },

  mounted() {},

  methods: {
    callback(eventName) {
      this.$emit("btnCallBack", eventName);
    },
  },
};
</script>

<style lang="scss" scoped>
.btn-box {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 7px 16px;
  background: #fff;
  border-bottom: 1px solid #eee;
  .left {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    span {
      margin-right: 20px;
    }
  }
  .right {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    span {
      margin-left: 20px;
    }
  }
}

:deep(.ant-btn) {
  padding: 4px 18px;
}
</style>
