<template>
  <div class="row-box">
    <a-row :gutter="24" v-if="type == 'default'">
      <a-col :sm="4" v-for="(i, idx) in data" :key="idx" class="g-col">
        <a-form-model-item
          v-if="i.dataType === 'String' || i.dataType === 'Fixed'"
          :label="i.dataPointName"
          :prop="`${validDataName}.${idx}.dataValue`"
          :rules="{
            message: '必填项不能为空',
            required: i.isRequired,
            trigger: 'change',
          }"
        >
          <a-input
            class="w-240"
            :name="i.dataPointName"
            :required="i.isRequired"
            :disabled="disabled"
            v-model:value="i.dataValue"
            :placeholder="`请输入${i.dataPointName}`"
          />
          <span
            v-if="i.dataType === 'Fixed'"
            :title="i.fixedValue"
            :class="{ hasError: i.dataValue && i.dataValue !== i.fixedValue }"
            >{{ i.fixedValue }}</span
          >
        </a-form-model-item>

        <!-- 条件判断2 -->
        <a-form-model-item
          v-if="i.dataType === 'Decimal' || i.dataType === 'Float'"
          :label="i.dataPointName"
          :prop="`${validDataName}.${idx}.dataValue`"
          :rules="{
            message: '必填项不能为空',
            required: i.isRequired,
            trigger: 'change',
          }"
        >
          <a-input-number
            v-model:value="i.dataValue"
            :step="1"
            :name="i.dataPointName"
            :required="i.isRequired"
            :disabled="disabled"
            class="w-240"
            :precision="4"
            :placeholder="`请输入${i.dataPointName}`"

          ></a-input-number>
          <!-- 是否允许超上下限 -->
          <span
            v-if="i.displayLimits"
            :title="i.lowerLimit"
            :class="{
              hasError:
                !i.isLimitOverrideAllowed &&
                i.dataValue !== null &&
                i.lowerLimit !== null &&
                i.lowerLimit !== undefined &&
                i.dataValue < i.lowerLimit,
            }"
            >{{ i.lowerLimit }}</span
          >
          <span
            v-if="i.displayLimits"
            :title="i.upperLimit"
            :class="{
              hasError:
                !i.isLimitOverrideAllowed &&
                i.dataValue !== null &&
                i.upperLimit !== null &&
                i.upperLimit !== undefined &&
                i.dataValue > i.upperLimit,
            }"
            >{{ i.upperLimit }}</span
          >
        </a-form-model-item>
        <!-- 条件判断3 -->
        <a-form-model-item
          v-if="i.dataType === 'Integer'"
          :label="i.dataPointName"
          :prop="`${validDataName}.${idx}.dataValue`"
          :rules="{
            message: '必填项不能为空',
            required: i.isRequired,
            trigger: 'change',
          }"
        >
          <a-input-number
            v-model:value="i.dataValue"
            step="1"
            :name="i.dataPointName"
            :required="i.isRequired"
            precisionMode="cut"
            :disabled="disabled"
            class="w-240"
            :precision="4"
            :placeholder="`请输入${i.dataPointName}`"

          >
          </a-input-number>

          <!-- 是否允许超上下限 -->
          <span
            v-if="i.displayLimits"
            :title="i.lowerLimit"
            :class="{
              hasError:
                !i.isLimitOverrideAllowed &&
                i.dataValue !== null &&
                i.lowerLimit !== null &&
                i.lowerLimit !== undefined &&
                i.dataValue < i.lowerLimit,
            }"
            >{{ i.lowerLimit }}</span
          >
          <span
            v-if="i.displayLimits"
            :title="i.upperLimit"
            :class="{
              hasError:
                !i.isLimitOverrideAllowed &&
                i.dataValue !== null &&
                i.upperLimit !== null &&
                i.upperLimit !== undefined &&
                i.dataValue > i.upperLimit,
            }"
            >{{ i.upperLimit }}</span
          >
        </a-form-model-item>

        <!-- 条件判断4 -->
        <a-form-model-item
          v-if="i.dataType === 'Timestamp'"
          :label="i.dataPointName"
          :prop="`${validDataName}.${idx}.dataValue`"
          :rules="{
            message: '必填项不能为空',
            required: i.isRequired,
            trigger: 'change',
          }"
        >
          <a-date-picker
            :name="i.dataPointName"
            :required="i.isRequired"
            v-model:value="i.dataValue"
            format="YYYY-MM-DD HH:mm:ss"
            value-format="YYYY-MM-DD HH:mm:ss"
            show-time
            style="width: 100%"
            :disabled="disabled"
            placeholder="选择日期"
          />
        </a-form-model-item>

        <!-- 条件判断5 -->
        <a-form-model-item
          v-if="i.dataType === 'Boolean'"
          :label="i.dataPointName"
          :prop="`${validDataName}.${idx}.dataValue`"
          :rules="{
            message: '必填项不能为空',
            required: i.isRequired,
            trigger: 'change',
          }"
        >
          <a-radio-group
            :name="i.dataPointName"
            v-model:value="i.dataValue"
            :disabled="disabled"
            :required="i.isRequired"
            class="radio-box"
          >
            <a-tooltip>
              <template #title>{{ i.booleanTrue }}</template>
              <a-radio value="true"
                ><span class="no-long-text">{{ i.booleanTrue }}</span></a-radio
              >
            </a-tooltip>
            <a-tooltip>
              <template #title>{{ i.booleanFalse }}</template>
              <a-radio value="false"
                ><span class="no-long-text">{{ i.booleanFalse }}</span></a-radio
              >
            </a-tooltip>
          </a-radio-group>
        </a-form-model-item>
      </a-col>
    </a-row>
    <!-- 工艺参数卡 -->
    <a-row v-if="type == 'ppcDataItemList'" :gutter="24">
      <a-col :sm="4" v-for="(i, idx) in data" :key="idx" class="g-col">
        <ul>
          <li class="card-item">
            <span class="no-long-text">序号</span>
            <span class="no-long-text">{{ idx + 1 }}</span>
          </li>
          <li class="card-item">
            <span class="no-long-text">参数卡名称</span>
            <span class="no-long-text">{{ i.dataPointName }}</span>
          </li>
          <li class="card-item">
            <span class="no-long-text">参数卡值</span>
            <span class="no-long-text">{{ i.dataValue }}</span>
          </li>
          <li class="card-item">
            <span class="no-long-text">单位</span>
            <span class="no-long-text">{{ i.itemUnit }}</span>
          </li>
        </ul>
      </a-col>
    </a-row>
  </div>
</template>

<script>
export default {
  name: "GDataPointView",
  props: {
    dataPoints: {
      type: Array,
    },
    validDataName: {
      type: String,
      default: "data",
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    type: {
      type: String,
      default: "default",
    },
  },
  data() {
    return {
      data: this.dataPoints,
    };
  },

  mounted() {},

  methods: {},
};
</script>

<style lang="scss" scoped>
.w-240 {
  width: 100%;
}

.hasError {
  color: red;
}

.card-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 30px;
  span {
    &:first-child {
      font-size: 14px;
      font-weight: bold;
    }
    &:last-child {
      font-size: 14px;
      color: #8f8f8f;
    }
  }
}

:deep(.ant-row) {
  .g-col {
    border-bottom: #eee 1px solid;
    border-right: 1px #eee solid;
    padding: 8px 24px 10px !important;

    &:nth-child(6) {
      border-right: none;
    }
  }
}

:deep(.radio-box) {
  width: inherit;
  display: block;
  margin-bottom: 11px;
  .ant-radio-group {
    width: inherit;
    > label {
      display: inline-block;
      width: 50%;
      margin-right: 0;
      span {
        &:nth-of-type(1) {
          display: inline-block;
        }
        &:nth-of-type(2) {
          width: inherit;
          display: contents;
          > span {
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            width: inherit;
            vertical-align: middle;
            margin-left: 5px;
            margin-bottom: 2px;
          }
        }
      }
    }
  }
}

:deep(.ant-calendar-picker) {
  min-width: auto !important;
}
</style>
