import Vue from "vue";
import { Modal } from "ant-design-vue";

// 获取扩展 Modal 组件
function getModalConstructor(modalProps, childConfig) {
  const { component, ...restConfig } = childConfig;
  const modalId = `modal-${Date.now()}`;

  return Vue.extend({
    mounted() {
      this.$nextTick(() => {
        this.initDrag();
      });
    },

    beforeDestroy() {
      // 清理事件监听
      const headerEl = document.querySelector(`.${modalId} .ant-modal-header`);
      if (headerEl) {
        headerEl.onmousedown = null;
      }
      document.onmousemove = null;
      document.onmouseup = null;
    },

    methods: {
      initDrag() {
        const headerEl = document.querySelector(`.${modalId} .ant-modal-header`);
        const modalEl = document.querySelector(`.${modalId} .ant-modal-content`);
        
        if (!headerEl || !modalEl) return;

        let startX = 0;
        let startY = 0;
        let initialTranslateX = 0;
        let initialTranslateY = 0;

        headerEl.style.cursor = 'move';
        headerEl.onmousedown = (e) => {
          e.preventDefault();
          startX = e.clientX;
          startY = e.clientY;

          const styles = window.getComputedStyle(modalEl);
          const matrix = new DOMMatrix(styles.transform);
          initialTranslateX = matrix.m41;
          initialTranslateY = matrix.m42;

          document.onmousemove = (e) => {
            const moveX = e.clientX - startX;
            const moveY = e.clientY - startY;
            modalEl.style.transform = `translate(${initialTranslateX + moveX}px, ${initialTranslateY + moveY}px)`;
          };

          document.onmouseup = () => {
            document.onmousemove = null;
            document.onmouseup = null;
          };
        };
      }
    },

    render(h) {
      return h(
        Modal,
        {
          class: [modalId, 'g-modal'],
          props: {
            visible: true,
            footer: null,
            ...modalProps,
          },
          on: {
            cancel: () => {
              this.$destroy();
            },
          },
        },
        [
          h(component, {
            props: {
              ...restConfig.props,
            },
            on: {
              closeModal: () => {
                this.$destroy();
              },
              success: (e) => {
                this.$destroy();
                restConfig.success && restConfig.success(e);
              },
              ...restConfig.on,
            },
          }),
        ]
      );
    },
  });
}

export default function createModal(modalProps, childConfig) {
  const ModalConstructor = getModalConstructor(modalProps, childConfig);
  const instance = new ModalConstructor().$mount();
  document.body.appendChild(instance.$el);
}

