<template>
  <div class="left">
    <aside class="father" :class="{ 'father-collapsed': collapsed }">
      <div class="father-menu">
        <a-menu :selectedKeys="fatherSelectedKeys" mode="inline" theme="dark" >
          <!-- 父子菜单 -->
          <template v-for="item in items">
            <a-menu-item
              :key="getPath('', item?.path)"
              @click="getChildrenPath(item)"
            >
              <i
                style="margin-bottom: 5px; font-size: 16px"
                :class="item.meta.icon"
                v-if="item.meta.icon"
              ></i>
              <span class="fs-12" v-if="!collapsed">
                {{ item?.meta?.title }}
              </span>
            </a-menu-item>
          </template>
        </a-menu>
      </div>

      <!-- 菜单展开/收起按钮 -->
       <div class="toggle-button-container">
        <div class="toggle-button" @click="toggleCollapsed">
          <a-icon :type="collapsed ? 'menu-unfold' : 'menu-fold'" />
        </div>
       </div>
    
     
    </aside>
    <aside class="children" v-if="childrenRoutes.length && !collapsed">
      <a-menu
        :selectedKeys="selectedKeys"
        :open-keys.sync="defaultOpenKeys"
        mode="inline"
        @select="handleClick"
        @openChange="openChange"
      >
        <!-- 子菜单 -->
        <template v-if="childrenRoutes.length" v-for="item in childrenRoutes">
          <a-sub-menu
            :key="item.path"
            @titleClick="titleClick"
            v-if="item.children && item.children.length > 0"
          >
            <span slot="title"
              >
              <i
              style="margin-bottom: 5px; font-size: 16px;margin-right:10px"
              :class="item.meta.icon"
              v-if="item.meta.icon"
            ></i>
              <span
                class="fs-12"
                >{{ item.meta.title }}
              </span></span
            >
            <template v-for="nav in item.children">
              <a-menu-item
                :key="nav.path"
                @click="goToPath(getPath(parentPath+'/'+item?.path, nav.path),item)"
              >
              <i
              style="margin-bottom: 5px; font-size: 16px;margin-right:10px"
              :class="item.meta.icon"
              v-if="item.meta.icon"
            ></i>
            <span class="fs-12">{{nav?.meta?.title}}
            </span>
              </a-menu-item>
            </template>
          </a-sub-menu>
          <a-menu-item
            :key="getPath('', item?.path)"
            @click="
              defaultOpenKeys = [];
              goToPath(parentPath + '/' + item.path,item);
            "
            v-else
          >
            <i
              style="margin-bottom: 5px; font-size: 16px;margin-right:10px"
              :class="item.meta.icon"
              v-if="item.meta.icon"
            ></i>
            <span 
            class="fs-12"
            >{{item?.meta?.title}}
            </span>
          </a-menu-item>
        </template>
      </a-menu>
    </aside>
    
    
  </div>
</template>

<script>
import store from "@/store";
import SliderBarItem from "./sliderBarItem.vue";
import { asyncRoutes } from "@/router";

export default {
  name: "DataV2Menu",
  data() {
    return {
      openKeys: [],
      items: [],
      level: 0,
      parentPath: "",
      defaultOpenKeys: [],
      fatherSelectedKeys: [],
      selectedKeys: [],
      collapsed: false, // 添加控制菜单展开/收起的状态
    };
  },
  computed: {
    filteredItems() {
      if (this.items?.children)
        return this.items?.children.filter((val) => !val.hidden);
      else return null;
    },

    childrenRoutes() {
      // 根据当前激活的路由找到对应的子路由
      const children = store.state.app.routers.filter(
        (val) => !val.hidden && val.path === this.parentPath && val.children
      );

      return children.length ? children[0].children : [];
    },
  },
  watch: {
    // 监听当前路由变化
    $route(to, from) {
      // Handle route change here
      console.log("Route changed from", from.path, "to", to.path);
      var r = to.path.split("/").filter((val) => val !== "");

      this.fatherSelectedKeys = ["/" + r[0]];
      this.parentPath = "/" + r[0];
      if (r[2]) {
        this.selectedKeys = [r[2]];
        this.defaultOpenKeys = [r[1]];
      } else if (r[1]) {
        this.selectedKeys = [r[1]];
        this.defaultOpenKeys = [];
      }
    },
  },
  mounted() {
    this.items = store.state.app.routers.filter((val) => !val.meta.hidden);
    var r = store.state.app.app.tabActive.split("/").filter((val) => val !== "");

    this.parentPath = "/" + r[0];
    this.fatherSelectedKeys = ["/" + r[0]];
    if (r[2]) {
      this.selectedKeys = [r[2]];
      this.defaultOpenKeys = [r[1]];
    } else if (r[1]) {
      this.selectedKeys = [r[1]];
    }
  },
  methods: {
    toggleCollapsed() {
      this.collapsed = !this.collapsed;
      if (this.collapsed) {
        this.defaultOpenKeys = [];
      }
    },
    handleClick(e) {
      this.selectedKeys = e.selectedKeys;
    },
    openChange(openKeys) {
      console.log(openKeys);
      // this.defaultOpenKeys = openKeys
    },
    getPath(parentPath, path) {
      if (parentPath) {
        return `${parentPath}/${path}`;
      } else {
        return path;
      }
    },
    goToPath(path,item) {
      this.$router.push(path);
      
    },
    titleClick(e) {
      console.log(e);
    },
    getChildrenPath(e) {
      const { path } = e;
      console.log(e);
      this.parentPath = path;
      this.fatherSelectedKeys = [path];
      this.defaultOpenKeys = [];
      this.selectedKeys = [];
      // 判断如果没有children属性就跳转
      if (!e.children) {
        this.goToPath(e.path);
      }
    },
  },
  components: { SliderBarItem },
};
</script>

<style lang="scss" scoped>
.left {
  height: 100%;
  overflow: auto;
  box-sizing: border-box;
  color: #fff;
  display: flex;
  
  .father {
    width: 80px;
    padding: 0 5px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    background-color: #001529;
    height: inherit;
    overflow-y: auto;
    transition: width 0.3s; /* 添加过渡效果 */

    .father-menu {
      flex: 1;
      overflow-y: auto;
      
      // .father 滚动条隐藏但可以滚动
      &::-webkit-scrollbar {
        display: none;
      }
    }
    
    &.father-collapsed {
      width: 50px; /* 收起时的宽度 */
    }
    
    
  }

  .children {
    width: 180px;
    height: 100%;
    overflow: auto;
    box-sizing: border-box;
    color: #fff;
    background-color: #fff;
    overflow-x: hidden;
    transition: width 0.3s; /* 添加过渡效果 */
  }
  
  /* 添加切换按钮样式 */
  .toggle-button {
    // position: absolute;
    // bottom: 5px;
    // right: 10px;
    width: 30px;
    height: 30px;
    background-color: #fff;
    color: #001529;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
   
    
    
    &:hover {
      background-color: #f0f0f0;
    }
  }
}

.toggle-button-container {
 width: 100%;
 display: flex;
 justify-content: center;
 align-items: center;
 padding: 10px 0 ;
}

.father .ant-menu-item {
  text-align: center !important;
  border-radius: 2px;
  height: auto !important;
  line-height: 20px !important;
  display: flex;
  align-items: center !important;
  flex-direction: column;
  padding: 12px 0px !important;
  justify-content: center !important;
}

.father .ant-menu-item .anticon {
  margin-right: 0 !important;
}

/* 添加菜单收起时的样式 */
.father-collapsed .ant-menu-item {
  padding: 8px 0px !important;
}
</style>
