import Vue from 'vue'
import Vuex from 'vuex'
import cache from './cache'
import app from './app'

Vue.use(Vuex)

export default new Vuex.Store({
  modules: {
    'cache': cache,
    'app': app
  },
  state: {
    isRenderTab: true
  },
  mutations: {
    setIsRenderTab (state, data) {
      state.isRenderTab = data
    }
  }
})