import { defineComponent } from "vue";
import AppConst from "../appConst";

const GNdoPageEditBase = defineComponent({
  props: {
    /** ndo编辑信息,此信息由 id/ndoBaseId/ndoId/ */
    ndoEditParams: { type: Object },
    /** save标记，用于触发编辑组件中的save方法 */
    saveTag: { type: String },
    /** 刷新标记，用于触发编辑组件中的refresh方法 */
    refreshTag: { type: String },
    /** 重置标记，用于触发编辑组建中的reset方法 */
    resetTag: { type: String },
  },
  data() {
    return {
      _ndoName: "",
      id: "",
      loading: false,

    };
  },
  mounted() {
    this.id = this.ndoEditParams?.id;
    this.init();
  },
  watch: {
    saveTag(newVal) {
      if (newVal) {
        this.save();
      }
    },
    refreshTag(newVal) {
      if (newVal) {
        this.resetPageForms();
        this.refresh();
      }
    },
    resetTag(newVal) {
      if (newVal) {
        this.reset();
      }
    },
  },
  computed: {
    
  },
  methods: {
    /** 重置表单 */
    resetPageForms() {
      if (this.$refs.form) {
        try {
          this.$refs.form.clearValidate();
        } catch (e) {}
      }
    },
    /** 重置编辑面板数据 */
    refresh(showMsg = true) {
      // 重置表单数据
      this.resetFormData();
      // 重置表单
      this.resetPageForms();
      // 初始化
      this.init();

      if (showMsg) {
        this.refreshSuccess();
      }
    },
    /** 刷新成功消息提示 */
    refreshSuccess(name, version) {
      if (!name) {
        this.$message.success(AppConst.message.refreshSuccess);
        return;
      }
      if (this.ndoEditParams) {
        this.$message.success(
          `${AppConst.message.refreshSuccess}-${name}(${version})-${this.$moment().format(
            "YYYY/MM/DD HH:mm:ss"
          )}`
        );
        
      }
    },
    /** 保存成功消息提示 */
    saveSuccess(name) {
      this._ndoName = name;

      // 创建或更新
      let msg = AppConst.message.createSuccess;
      if (this.id) {
        msg = AppConst.message.updateSuccess;
      }
      if (this.ndoEditParams) {
        this.$message.success(
          `${msg}-${name}-${this.$moment().format("YYYY/MM/DD HH:mm:ss")}`
        );
      }
    //   this.refresh(false);
    },
    /** 根据参数初始化组件数据 需要重写*/
    init() {},
    /** 重置表单绑定的数据源  抽象方法，需要子类重写*/
    resetFormData() {},
    /** 保存编辑面板数据  抽象方法，需要重写*/
    save() {},
    /** 重置 */
    reset() {
      // 重置输入参数
      this.id = null;
      this.operationName = undefined;
      this.operationTime = undefined;
      // 重置表单数据
      this.resetFormData();
      // 重置表单之后
      this.resetPageForms();
    },
    /**
     * 给父级反馈并点击成编辑模式
     * @param result 返回的id
     */
    handleSaveEndBackView(id) {
      this.$emit("rowClick", {
        id: id,
        name: this._ndoName,
      });
    },
    /** 刷新成功消息提示 */
    refreshParent(params) {
      if (!params) {
        params = {};
      }
      if (params.reset) {
        this.reset();
      }
      this.$emit("childRefresh", params);
    },
    checkPageForms() {
      return new Promise((resolve, reject) => {
        this.$refs.form
          .validate()
          .then(() => {
            resolve(true);
          })
          .catch((err) => {
            reject(err);
          });
      });
    },
    // 处理检验异常
    async checkError() {
      try {
        let valid = await this.checkPageForms();
        return valid;
      } catch (error) {
        return;
      }
    },
  },
});

export default GNdoPageEditBase;
