<template>
  <div class="g-sign-container" ref="signContainer">
    <canvas
      ref="signCanvas"
      class="sign-canvas"
      @mousedown="startDrawing"
      @mousemove="draw"
      @mouseup="stopDrawing"
      @mouseleave="stopDrawing"
      @touchstart="handleTouchStart"
      @touchmove="handleTouchMove"
      @touchend="stopDrawing"
    ></canvas>
    <div class="sign-actions">
      <button class="clear-btn" @click="clearCanvas">Clear</button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'GSign',
  props: {
    lineWidth: {
      type: Number,
      default: 2,
    },
    lineColor: {
      type: String,
      default: '#000000',
    },
    backgroundColor: {
      type: String,
      default: '#f50', // Corrected default to white
    },
    saveFormat: {
      type: String,
      default: 'png',
      validator: (value) => ['png', 'jpeg', 'webp'].includes(value),
    },
  },
  data() {
    return {
      isDrawing: false,
      lastX: 0,
      lastY: 0,
      ctx: null,
      hasSignature: false,
      //imageData: null, // Removed: We handle drawing and background separately now.
    };
  },
  mounted() {
    this.$nextTick(() => {
      this.initCanvas();
    });
    window.addEventListener('resize', this.resizeCanvas);
  },
  beforeUnmount() {
    window.removeEventListener('resize', this.resizeCanvas);
  },
  watch: {
    lineWidth() {
      this.updateCanvasStyle();
    },
    lineColor() {
      this.updateCanvasStyle();
    },
    backgroundColor() {
      this.clearCanvas();
      this.updateCanvasStyle(); // Call updateCanvasStyle when backgroundColor changes
    },
  },
  methods: {
    initCanvas() {
      const canvas = this.$refs.signCanvas;
      const container = this.$refs.signContainer;

      canvas.width = container.clientWidth;
      canvas.height = container.clientHeight - 50;

      this.ctx = canvas.getContext('2d');
      this.resetCanvas(); // Use a single function for consistent setup

    },
    resetCanvas() {
        const canvas = this.$refs.signCanvas;
        // 1. Clear any existing drawing (very important!)
        this.ctx.clearRect(0, 0, canvas.width, canvas.height);
        // 2. Set the background color
        this.ctx.fillStyle = this.backgroundColor;
        this.ctx.fillRect(0, 0, canvas.width, canvas.height);
        // 3. Set drawing styles
        this.ctx.lineWidth = this.lineWidth;
        this.ctx.strokeStyle = this.lineColor;
        this.ctx.lineCap = 'round';
        this.ctx.lineJoin = 'round';
    },

    resizeCanvas() {
      const canvas = this.$refs.signCanvas;
      const container = this.$refs.signContainer;
      //Crucial: We capture the signature image BEFORE resizing the canvas
      const tempCanvas = document.createElement('canvas');
      tempCanvas.width = canvas.width;
      tempCanvas.height = canvas.height;
      const tempCtx = tempCanvas.getContext('2d');
      tempCtx.drawImage(canvas,0,0); //Copy the canvas image.

      canvas.width = container.clientWidth;
      canvas.height = container.clientHeight - 50;
      this.resetCanvas();// Reset, including background and styles.
        //Redraw the image
      this.ctx.drawImage(tempCanvas,0,0);
    },

    startDrawing(event) {
      this.isDrawing = true;
      const { offsetX, offsetY } = event;
      this.lastX = offsetX;
      this.lastY = offsetY;
      this.ctx.beginPath(); // Start a new path *here*
    },

    draw(event) {
      if (!this.isDrawing) return;

      const { offsetX, offsetY } = event;
      // No beginPath here, it's in startDrawing and handleTouchStart
      this.ctx.moveTo(this.lastX, this.lastY);
      this.ctx.lineTo(offsetX, offsetY);
      this.ctx.stroke();

      this.lastX = offsetX;
      this.lastY = offsetY;
      this.hasSignature = true;
    },

    handleTouchStart(event) {
      event.preventDefault();
      if (event.touches.length !== 1) return;

      const touch = event.touches[0];
      const rect = this.$refs.signCanvas.getBoundingClientRect();
      const offsetX = touch.clientX - rect.left;
      const offsetY = touch.clientY - rect.top;

      this.isDrawing = true;
      this.lastX = offsetX;
      this.lastY = offsetY;
      this.ctx.beginPath(); // Start a new path *here*
    },

    handleTouchMove(event) {
      event.preventDefault();
      if (!this.isDrawing || event.touches.length !== 1) return;

      const touch = event.touches[0];
      const rect = this.$refs.signCanvas.getBoundingClientRect();
      const offsetX = touch.clientX - rect.left;
      const offsetY = touch.clientY - rect.top;

      // No beginPath here
      this.ctx.moveTo(this.lastX, this.lastY);
      this.ctx.lineTo(offsetX, offsetY);
      this.ctx.stroke();

      this.lastX = offsetX;
      this.lastY = offsetY;
      this.hasSignature = true;
    },

    stopDrawing() {
      this.isDrawing = false;
       this.saveSignature();// Removed:  We save on stop drawing anyway.
    },

    clearCanvas() {
      this.resetCanvas();
      this.hasSignature = false;
      this.$emit('clear');
    },
     saveSignature() {
      if (!this.hasSignature) {
        this.$emit('save-error', 'Please sign first.');
        return;
      }

      const canvas = this.$refs.signCanvas;
       // Use toBlob for better performance and control over quality
        canvas.toBlob(blob => {
            if (blob) {
                const reader = new FileReader();
                reader.onloadend = () => {
                    this.$emit('save', reader.result); // Emit base64 data
                };
                reader.readAsDataURL(blob);
            } else {
                this.$emit('save-error', 'Failed to create signature image.');
            }
        }, `image/${this.saveFormat}`);

    },

    updateCanvasStyle() {
      if (!this.ctx) return;
       const canvas = this.$refs.signCanvas;
       //Crucial: We capture the signature image BEFORE resizing the canvas
      const tempCanvas = document.createElement('canvas');
      tempCanvas.width = canvas.width;
      tempCanvas.height = canvas.height;
      const tempCtx = tempCanvas.getContext('2d');
      tempCtx.drawImage(canvas,0,0); //Copy the canvas image.
       this.resetCanvas(); // Reset, including background and styles.
        //Redraw the image
      this.ctx.drawImage(tempCanvas,0,0);
    },
  },
};
</script>

<style scoped>
/*  (Your existing styles, no changes needed here) */
.g-sign-container {
  width: 100%;
  height: 100%;
  border: 1px solid #dcdfe6;
  border-radius: 4px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.sign-canvas {
  flex: 1;
  cursor: crosshair;
  touch-action: none;
}

.sign-actions {
  display: flex;
  justify-content: space-between; /* Consistent spacing */
  padding: 8px;
  background-color: #f5f7fa;
  height: 50px;
  box-sizing: border-box;
}

.clear-btn{
  padding: 6px 16px;
  border: 1px solid #dcdfe6;
  border-radius: 4px;
  background-color: #fff;
  cursor: pointer;
  font-size: 14px;
  transition: all 0.3s;
}

.clear-btn:hover {
  color: #409eff;
  border-color: #c6e2ff;
  background-color: #ecf5ff;
}
</style>