<template>
  <!-- 折叠面板 -->
  <a-collapse
    v-model="activeKeyMenu"
    :bordered="false"
    :expandIconPosition="'right'"
    class="g-collapse"
  >
    <a-collapse-panel key="1">
      <template #header>
        <div style="display: flex; align-items: center">
          <div class="header-icon"></div>
          <span style="padding-left: 10px">
            {{ headerName }}
          </span>
        </div>
      </template>
      <slot name="content"></slot>
    </a-collapse-panel>
  </a-collapse>
</template>

<script>
export default {
  name: "DataV2GCollapse",
  props: {
    headerName: {
      type: String,
    },
    activeKey: {
      type: Array,
      default: () => {
        return [1];
      },
    },
  },
  data() {
    return {
      activeKeyMenu: [],
    };
  },

  mounted() {
    this.activeKeyMenu = this.activeKey;
  },

  methods: {},
};
</script>

<style lang="scss" scoped>
.header-icon {
  width: 4px;
  height: 16px;
  background: #007bff;
  border-radius: 4px;
  display: inline-block;
  position: absolute;
  margin-right: 12px;
  color: #007bff;
}

:deep(.ant-collapse-header) {
  border-bottom: 1px solid #eee;
  display: flex;
  align-items: center !important;
  padding: 6px 16px 6px 16px !important;
  padding-right: 40px !important;
}

.g-collapse {
  display: block;
  margin-bottom: 6px;
  border-radius: 2px;
  background: #fff !important;
}

.ant-collapse-borderless > .ant-collapse-item {
  border-bottom: none;
}

:deep(.ant-collapse > .ant-collapse-item.ant-collapse-no-arrow > .ant-collapse-header) {
    padding: 12px 24px;
  }

  :deep(.ant-collapse > .ant-collapse-item-active > .ant-collapse-header) {
    border-bottom: 1px solid #eeeeee;
  }

  :deep(.ant-collapse-content-box) {
    padding: 15px 24px 8px !important;
  }

  :deep(.ant-collapse-borderless
    > .ant-collapse-item
    > .ant-collapse-content
    > .ant-collapse-content-box) {
  padding-top: 15px;
}
</style>
