<template>
  <div class="suffix-search">
    <a-input-search
      placeholder="请输入查询"
      enter-button
      @search="keyupCallback"
      @change="onChange"
      @keyup.enter="keyupCallback"
      v-model:value="filterText"
      :maxLength="200"
    >
    </a-input-search>
  </div>
</template>

<script>
export default {
  name: "GSuffixSearch",
  props: {
    searchVal: {
      type: String,
      default: () => "",
    },
  },
  data() {
    return {
      filterText: this.searchVal,
    };
  },

  mounted() {},

  methods: {
    onChange(e) {
      this.$emit("onSearchValChange", e.target.value);
    },
    keyupCallback(e) {
      const queryConditions = [];
      const input = new Object();
      if (this.filterText) {
        input.value = this.filterText;
        input.field = "name";
        queryConditions.push(input);
      }
      this.$emit("onSearch", queryConditions);
    },
  },
};
</script>

<style lang="scss" scoped></style>
