<template>
<a-button :type="type" :disabled="disabled" @click="callback" >
    <div class="btn-box">
      <i
        v-if="icon"
        :class="icon"
        style="font-size: 20px"
      ></i>
      {{ text }}
    </div>
  </a-button>
</template>

<script>
import debounce from 'lodash/debounce';

export default {
  name: "DataV2GBtn",
  props: {
    type: {
      type: String,
      default: "default",
    },
    text: {
      type: String,
      default: "按钮",
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    icon: {
      type: String,
      default: "",
    },
    eventName: {
      type: String,
      default: "",
    }
  },
  data() {
    return {};
  },

  mounted() {},

  methods: {
    callback: debounce(function() {
      this.$emit("callback",this.eventName)
    }, 300)
  },
};
</script>

<style lang="scss" scoped>
.btn-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.ant-btn {
  height: auto;
  min-height: 32px;
    padding: 4px 16px;
}
</style>
