<template>
  <a-table
    :columns="columns"
    :data-source="selfDataList"
    :pagination="false"
    :scroll="{ y: scrollY }"
    :loading="isTableLoading"
    class="ndo-table"
    id="ndo-table"
    @change="onChange"
    :rowKey="(record) => record.id"
  >
    <!-- 用于设置表头的提示信息 -->
    <span slot="customTitle"
      >{{ handName }}
      <span class="pageSize-box">{{ selfDataList?.length }}/{{ total }}</span>
    </span>
    <template slot="name" slot-scope="text, record">
      <div class="flex-box">
        <div class="expand-icon" @click="expandChange(record)">
          <i v-if="!record.expand" class="ri-arrow-right-s-fill"></i>
          <i v-else class="ri-arrow-down-s-fill"></i>
        </div>
        <div class="table-level-row" @click="rowClicked(record, null)">
          {{ record.name }}
        </div>
      </div>
      <!--   展开的行   -->
      <div v-if="record.expand" class="expand-row">
        <ul>
          <li
            v-for="(item, index) in record.rdoList"
            :class="{ 'row-checked': selfRdoId == item.id }"
            @click="rowClicked(record, item)"
          >
            <span> {{ record.name }}: {{ item.revision }} </span>
            <!-- 选择框 -->
            <a-checkbox
              v-if="record.revOfRcdId == item.id"
              :checked="record.revOfRcdId == item.id"
              :disabled="true"
              class="expand-row-checkbox"
            >
            </a-checkbox>
          </li>
        </ul>
      </div>
    </template>
  </a-table>
</template>

<script>
import { getTableScroll } from "@/utils/getTableScroll";

export default {
  name: "GNdoTable",
  props: {
    // 表头名称
    handName: {
      type: String,
      default: "",
    },
    // 数据
    dataList: {
      type: Array,
      default: () => {
        return [];
      },
    },
    // 是否加载
    isTableLoading: {
      type: Boolean,
      default: false,
    },
    // 数据条数
    total: {
      type: Number,
      default: 0,
    },
    rdoId: {
      type: String | Number,
    },
    expendResetTag: {
      type: String,
      default: "",
    }
  },
  data() {
    return {
      columns: [
        {
          dataIndex: "name",
          sorter: true,
          width: "20%",
          key: "name",
          ellipsis: true,
          scopedSlots: { customRender: "name" },
          slots: { title: "customTitle" },
        },
      ],
      scrollY: 100,
      entityId: null,
      expands: {} /** 缓存记录所有展开的rdoBaseId */,
      selfDataList: [],
      lastClickRow: {
        id: "",
        rdoBaseId: "",
        rdoId: "",
        isRevOfRcdId: false,
        name: "",
        version: "",
      },
      selfRdoId: undefined,
      _rdoId: this.rdoId,
    };
  },
  watch: {
    rdoId(val) {
      this.entityId = val;
      this.selfRdoId = undefined;
        // 如果有值，给到内部id
        if (val) {
          this.selfRdoId = val;
        } else if (this.lastClickRow && this.pageMode === 'edit') {
          // 传入rdo无值，但是最后一次点击有值,并且当前处于编辑模式,使用最后点击项的id
          this.selfRdoId = this.lastClickRow.rdoId;
        }
    },
    dataList(val) {
      if (val) {
        this.processorDataList(val);
      }
    },
    expendResetTag() {
      this.expands = {};
      // 收起
      if (this.expands) {
        this.processorDataList(this.dataList);
      }
    }
  },

  mounted() {
    this.$nextTick(() => {
      this.scrollY = getTableScroll({ id: "ndo-table" }, 48);
    });
  },

  methods: {
    onChange(e) {
      console.log("onChange", e);
    },
    expandChange(record, e) {
      // e.stopPropagation();

      if (this.expands) {
        this.expands[record.id] = record.expand;

        record.expand = e == "setTrue" ? true : !record.expand;
      }
    },
    rowClicked(rdoBase, rdo = null) {
      if (rdoBase) {
        this.expandChange(rdoBase, "setTrue");
      }
      // 是否为默认版本
      let isRevOfRcdId = false;
      // 如果没有传入rdo, 从rdo中查找默认版本的rdo
      if (!rdo) {
        rdo = rdoBase?.rdoList?.find((o) => o.id === rdoBase.revOfRcdId);
        isRevOfRcdId = true;
      } else {
        isRevOfRcdId = rdoBase.revOfRcdId === rdo.id;
      }

      this.entityId = rdoBase.id;
      this._rdoId = rdo?.id;
      this.selfRdoId = rdo?.id;

      this.lastClickRow = {
        id: rdoBase.id,
        rdoBaseId: rdoBase.id,
        rdoId: rdo?.id,
        isRevOfRcdId: isRevOfRcdId,
        name: rdoBase.name,
        version: rdo?.revision,
      };
      console.log("rowClick", this.lastClickRow);
      this.$emit("rowClick", this.lastClickRow);
    },
    /** 处理输入数据 */
    processorDataList(input) {
      this.selfDataList = [];
      if (!input) {
        return;
      }
      this.selfDataList = input.map((o) => {
        let res = { ...o };
        if (this.expands && this.expands[res.id]) {
          res.expand = true;
        } else {
          res.expand = false;
        }
        return res;
      });
    },
  },
};
</script>

<style lang="less" scoped>
.ndo-table {
  overflow-y: auto;
}
:deep(.ant-table-placeholder) {
  border-bottom: none;
}

:deep(.ant-table-row) {
  cursor: pointer;
}

:deep(.ant-table-tbody > tr > td) {
  padding: 0;
}



.row-td {
  padding: 11px 24px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.flex-box {
  display: flex;
  height: 45px;
  align-items: center;
  cursor: pointer;
  padding-left: 16px;
  .expand-icon {
    margin-right: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    i {
      font-size: 18px;
    }
  }
  .table-level-row {
    flex: 1;
  }
}
.expand-row {
  ul {
    padding: 0;
    li {
      width: 100%;
      border-top: 1px solid #f0f0f0;
      padding-left: 16px;
      list-style: none;
      height: 45px;
      line-height: 45px;
      display: flex;
      align-items: center;
      font-size: 12px;
      color: #666;
      cursor: pointer;
      background-color: #fbfbfb;
      span {
        margin-left: 2px;
      }
      .expand-row-checkbox {
        margin-left: 10px;
      }
    }
  }
}

.row-checked {
  color: @primary-color;
  background: #ebf2fa !important;
  font-size: 14px !important;
  font-weight: bold;
  span {
    color: @primary-color;
  }
}
</style>
