<template>
    <div>
        <i
            class="ri-lock-2-line mr-2"
            style="font-size: 18px; cursor: pointer"
            v-if="!locked"
            @click="lockScreen"
            title="锁屏"
        ></i>
        <button v-else @click="unlockScreen" class="unlock-button">解锁</button>
        <div v-if="locked" class="lock-screen">
            <div class="lock-screen-content">
                <svg
                    version="1.1"
                    xmlns="http://www.w3.org/2000/svg"
                    xmlns:xlink="http://www.w3.org/1999/xlink"
                    width="300px"
                    height="300px"
                >
                    <polygon
                        class="g-polygon-wrap"
                        points="150 0, 300 75, 300 225, 150 300, 0 225,  0 75, 150 0"
                    />
                    <polygon
                        class="g-polygon-move"
                        points="150 0, 300 75, 300 225, 150 300, 0 225,  0 75, 150 0"
                    />
					<text class="locked-text" x="50%" y="50%" text-anchor="middle" dominant-baseline="middle">已锁屏</text>
                </svg>
                <input
                    type="password"
                    v-model="password"
                    placeholder="请输入密码"
                    class="password-input"
                />
                <button @click="unlockScreen" class="unlock-button">
                    解锁
                </button>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            locked: false,
            password: "",
            charging: false,
        };
    },
    methods: {
        lockScreen() {
            this.locked = true;
            this.startChargingAnimation();
        },
        unlockScreen() {
            if (this.password === "123456") {
                this.locked = false;
                this.password = "";
                this.stopChargingAnimation();
            } else {
                alert("密码错误");
            }
        },
        startChargingAnimation() {
            this.charging = true;
        },
        stopChargingAnimation() {
            this.charging = false;
        },
    },
};
</script>

<style>
.lock-button,
.unlock-button {
    padding: 10px 20px;
    font-size: 16px;
    border: none;
    border-radius: 5px;
    background-color: #007bff;
    color: #fff;
    cursor: pointer;
}

.lock-button:hover,
.unlock-button:hover {
    background-color: #0056b3;
}

.lock-screen {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 999;
}

.lock-screen-content {
    padding: 20px;
    border-radius: 5px;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.lock-screen-content h2 {
    margin-bottom: 10px;
}

.password-input {
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-bottom: 20px;
    width: 200px;
    margin-top: 20px;
}

.unlock-button {
    width: 200px;
    background-color: #28a745;
}

.unlock-button:hover {
    background-color: #1e7e34;
}

svg {
    margin: auto;
    overflow: visible;
    transform: scalex(0.9);
}

.g-polygon-wrap,
.g-polygon-move {
    fill: none;
    stroke: #308fbf;
    stroke-width: 2;
    stroke-linejoin: round;
    stroke-linecap: round;
}

.g-polygon-move {
    transform-origin: center center;
    transform: scale(1.05);
    stroke: linear-gradinet(180deg, red, transprent);
    stroke-width: 1.5;
    stroke-dasharray: 280, 700;
    stroke-dashoffset: 8;
    animation: move 2.4s infinite linear;
}

@keyframes move {
    0% {
        stroke-dashoffset: 8;
    }
    100% {
        stroke-dashoffset: -972;
    }
}

.locked-text {
  font-size: 24px;
  fill: #fff;
}
</style>
