let selectRow = [];

export function getDataAPI({ pageNum, pageSize, keyword, selectId }) {
  return new Promise((resolve) => {
    setTimeout(() => {
      let listData = [];
      data.forEach((item) => {
        if (keyword) {
          if (item.label.indexOf(keyword) !== -1) {
            listData.push(item);
          }
        } else {
          listData.push(item);
        }
      });
      // 如果没有传入selectId，就不需要过滤
      if (selectId) {
        selectRow = listData.filter((item) => item.label == selectId);
        listData = listData.filter((item) => item.label != selectId);
      }
      const start = (pageNum - 1) * pageSize;
      const end = pageNum * pageSize;
      resolve({
        rows: listData.slice(start, end),
        selectRow: selectRow,
        total: listData.length,
      });
    }, 500);
  });
}
