<template>
    <div class="tags">
        <div class="tabs">
            <div
                style="
                    width: 40px;
                    height: 40px;
                    display: flex;
                    font-size: 22px;
                    align-items: center;
                    justify-content: center;
                    flex: none;
                "
            >
                <!-- 左箭头 -->
                <i
                    class="ri-arrow-left-s-line icon-left"
                    style="cursor: pointer"
                    @click="moveLeftTabs"
                ></i>
            </div>
            <div class="nav-content">
                <div class="nav-list">
                    <div
                        :class="{
                            'tab-item': true,
                            'tab-active': tabActive == item.fullPath,
                        }"
                        v-for="(item, i) in tabList"
                        :data-index="i"
                        @click="goToPage(item.fullPath, i)"
                    >
                        {{ item.meta.title }}

                        <i
                            class="ri-close-fill icon-close"
                            v-if="item.meta.title != '工作台'"
                            @click.stop="closeTab(item.fullPath)"
                        ></i>
                    </div>
                </div>
            </div>

            <div
                style="
                    height: 40px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    flex: none;
                "
            >
                <!-- 右箭头 -->
                <i
                    class="ri-arrow-right-s-line icon-right"
                    style="
                        font-size: 22px;
                        cursor: pointer;
                        width: 40px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                    "
                    @click="moveRightTabs"
                ></i>
                <a-dropdown>
                    <i
                        class="ri-apps-fill"
                        style="font-size: 20px; margin-right: 10px"
                    />
                    <a-menu slot="overlay">
                        <!-- 刷新标签 -->

                        <a-menu-item>
                            <a href="javascript:;" @click="refreshTab"
                                >刷新标签</a
                            >
                        </a-menu-item>

                        <a-menu-item>
                            <a href="javascript:;" @click="closeTab(tabActive)"
                                >关闭当前标签</a
                            >
                        </a-menu-item>
                        <a-menu-item>
                            <a href="javascript:;" @click="closeOtherTabs"
                                >关闭其它标签</a
                            >
                        </a-menu-item>
                    </a-menu>
                </a-dropdown>
            </div>
        </div>
    </div>
</template>

<script>
import store from "@/store";
import { mapActions } from "vuex";

export default {
    name: "DataV2Tags",

    data() {
        return {
            transformNum: 1,
        };
    },
    computed: {
        tabList() {
            return store.state.app.app.tabsList;
        },
        tabActive() {
            // 如果新打卡标签页是最后一个直接最大距离
            console.log(
                store.state.app.app.tabsList[
                    store.state.app.app.tabsList.length - 1
                ].fullPath
            );
            if (
                store.state.app.app.tabActive ==
                store.state.app.app.tabsList[
                    store.state.app.app.tabsList.length - 1
                ].fullPath
            ) {
                console.log("最后一个");
            }
            this.$nextTick(() => {
                const index = store.state.app.app.tabsList.findIndex(
                    (item) => item.fullPath === store.state.app.app.tabActive
                );

                const navContent = document.querySelector(".nav-content");
                var navList = document.querySelector(".nav-list");

                if (index !== -1) {
                    console.log("已打开",index);
                    // 获取已打开标签页的DOM元素
                    const openedTab = document.querySelector(
                        `.tab-item[data-index="${index}"]`
                    );
                    const isVisible = this.isBoxVisibleInContainer(
                        navContent,
                        openedTab
                    );
                    console.log(isVisible);
                    if (!isVisible) {
                        // 平移到指定元素的位置
                        const offsetLeftMove = openedTab.offsetLeft + openedTab.offsetWidth;
                        const navListWidth = navList.offsetWidth;

						const maxWidth = -Math.abs(
							navContent.offsetWidth - navList.offsetWidth
						);
                        

                        console.log(
                            "的位置：",
                            navListWidth,
							
                        );
                        
						console.log("offsetLeft：", offsetLeftMove);
                        // navList
							
                        // 计算打开的标签页相对于navContent视口的位置
                        
                        const scrollDistance = Math.ceil(
                            navListWidth - offsetLeftMove + maxWidth
                        );

                        console.log("滚动距离右边：", scrollDistance);
						if(index==0) {
							document.querySelector(
								".nav-list"
							).style.transform = `translate(0px, 0px)`;
							return;
						}
						if(scrollDistance > 0) {
							
							document.querySelector(
                            ".nav-list"
                        ).style.transform = `translate(-${openedTab.offsetLeft}px, 0px)`;
							return;
						}
                        document.querySelector(
                            ".nav-list"
                        ).style.transform = `translate(${scrollDistance}px, 0px)`;
                    }
                } else {
                    console.log("未打开");
                }
            });

            return store.state.app.app.tabActive;
        },
        caches() {
            return store.state.cache.caches;
        },
    },

    mounted() {
        store.dispatch("app/initTabs");
    },

    methods: {
        isBoxVisibleInContainer(container, box) {
            const containerRect = container.getBoundingClientRect();
            const boxRect = box.getBoundingClientRect();

            // 检查盒子是否在父容器的可视区域内
            const isVerticalVisible =
                boxRect.top <= containerRect.bottom &&
                boxRect.bottom >= containerRect.top;
            const isHorizontalVisible =
                boxRect.left+box.offsetWidth < containerRect.right &&
                boxRect.right-box.offsetWidth > containerRect.left;

            return isVerticalVisible && isHorizontalVisible;
        },
        goToPage(path, index) {
            this.$router.push(path);
            store.dispatch("app/clickTab", index);
        },
        closeTab(item) {
            // 判断是否为最后一个
            if (this.tabList.length == 1) {
                this.$message.info("当前是最后一个");
                return;
            }
            store.dispatch("cache/removeCache", this.$route.name || "");
            // 判断关闭的是不是当前路由
            store.dispatch("app/closeCurrentTab", {
                fullPath: item,
                type: this.tabActive == item ? "current" : "other",
            });
        },
        closeOtherTabs() {
            store.dispatch("app/closeOtherTabs", { fullPath: this.tabActive });
           setTimeout(() => {
            // 位置到最左边
                document.querySelector(".nav-list").style.transform = `translate(0px, 0px)`;
            }, 50);
        },
        refreshTab() {
            // 改变刷新标记
            store.commit("app/SET_REFRESH", false);

            const index = this.caches.indexOf(this.$route.name);
            if (index > -1) {
                // 清除缓存
                this.caches.splice(index, 1);
            }
            this.$nextTick(() => {
                this.caches.push(this.$route.name); // 重新添加缓存
                store.commit("app/SET_REFRESH", true);
            });
        },
        moveLeftTabs() {
            // 移动 .nav-list transform: translate(-724px, 0px);
            // .nav-content 的宽度
            const navContentWidth =
                document.querySelector(".nav-content").offsetWidth;
            const navListWidth =
                document.querySelector(".nav-list").offsetWidth;
            // 最大右边移动距离 变成负数
            if (this.transformNum <= 0) {
                return;
            }
            this.transformNum =
                this.transformNum == 1
                    ? this.transformNum - 1
                    : this.transformNum - 1 - 1;

            const maxWidth = -Math.abs(navContentWidth - navListWidth);

            if (
                this.transformNum * -100 > maxWidth &&
                this.transformNum * -100 <= 0
            ) {
                document.querySelector(
                    ".nav-list"
                ).style.transform = `translate(${
                    this.transformNum * -100
                }px, 0px)`;
            }

            if (this.transformNum == 0) {
                this.transformNum = 1;
            }
        },
        moveRightTabs() {
            // .nav-content 的宽度
            const navContentWidth =
                document.querySelector(".nav-content").offsetWidth;
            const navListWidth =
                document.querySelector(".nav-list").offsetWidth;
            // 最大右边移动距离 变成负数

            const maxWidth = -Math.abs(navContentWidth - navListWidth);
            // 不能超过最大距离

            if (
                this.transformNum * -100 > maxWidth &&
                navListWidth >= navContentWidth
            ) {
                document.querySelector(
                    ".nav-list"
                ).style.transform = `translate(${
                    this.transformNum * -100
                }px, 0px)`;
                this.transformNum = this.transformNum + 1;
            } else if (navListWidth >= navContentWidth) {
                // 直接最大距离
                document.querySelector(
                    ".nav-list"
                ).style.transform = `translate(${maxWidth}px, 0px)`;
            }
        },
    },
};
</script>

<style lang="less" scoped>
.tags {
    background-color: #fff;
    margin-bottom: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 1px 4px rgba(0, 21, 41, 0.08);
}

.tabs {
    display: flex;
    align-items: center;
    width: 100%;
}

.tab-item {
    cursor: pointer;
    height: 40px;
    line-height: 40px;
    padding: 0 16px;
    display: flex;
    justify-content: space-between;
    // 超出...
    text-overflow: ellipsis;
    white-space: nowrap;

    &:hover {
        color: @primary-color;
    }
}

.tab-active {
    background-color: #e6f7ff;
    color: @primary-color;
    border-bottom: 2px solid @primary-color;
}

.icon-close {
    margin-left: 8px;
    color: #00000073;

    &:hover {
        color: #333;
    }
}

.nav-content {
    position: relative;
    display: inline-block;
    display: flex;
    flex: auto;
    align-self: stretch;
    overflow: hidden;
    white-space: nowrap;
    transform: translate(0);
    &::before {
        position: absolute;
        z-index: 1;
        opacity: 1;
        transition: opacity 0.3s;
        content: "";
        pointer-events: none;
        left: 0;
        box-shadow: inset 10px 0 8px -8px rgba(0, 0, 0, 0.15);
        top: 0;
        bottom: 0;
        width: 30px;
    }
    &::after {
        position: absolute;
        z-index: 1;
        opacity: 1;
        transition: opacity 0.3s;
        content: "";
        pointer-events: none;
        right: 0;
        // 只有右边有内阴影
        box-shadow: inset -10px 0 8px -8px rgba(0, 0, 0, 0.15);
        top: 0;
        bottom: 0;
        width: 30px;
    }

    .nav-list {
        position: relative;
        display: flex;
        transition: transform 0.3s;
    }
}
</style>
