<template>
  <div id="chart" ref="chart" :style="style"></div>
</template>

<script>
import * as echarts from "echarts";
import elementResizeDetectorMaker from "element-resize-detector";
export default {
  name: "BaseChart",
  props: {
    // 图表的宽度
    width: {
      type: String,
      default: "600px",
    },
    // 图表的高度
    height: {
      type: String,
      default: "400px",
    },
    // 图表的配置
    option: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      chart: null,
      // 默认设计稿的宽度
      designWidth: 1920,
      // 默认设计稿的高度
      designHeight: 1080,

      // px 转为 vw 的函数
    };
  },
  computed: {
    style() {
      return {
        width: this.vw(this.width) + "vw",
        height: this.vh(this.height) + "vh",
      };
    },
  },
  watch: {
    option: {
      deep: true,
      handler() {
        this.initChart();
      },
    },
  },
  mounted() {
    this.initChart();
    this.handleResize();
  },
  beforeDestroy() {
    if (!this.chart) {
      return;
    }
    // 销毁图表实例
    this.chart.dispose();
    this.chart = null;
  },
  methods: {
    // 初始化图表
    initChart() {
      // 如果没有图表实例，则创建一个新的实例
      if (!this.chart) {
        this.chart = echarts.init(this.$refs.chart);
      }
      this.chart.setOption(this.option);
    },
    // 处理图表容器大小变化
    handleResize() {
      const erd = elementResizeDetectorMaker();
      // 监听图表容器的大小变化
      erd.listenTo(this.$refs.chart, () => {
        // 改变图表尺寸
        this.chart.resize();
      });
    },
    vw(px) {
      return (px / this.designWidth) * 100;
    },
    vh(px) {
      return (px / this.designHeight) * 100;
    },
  },
};
</script>

<style lang="scss" scoped></style>
