<template>
  <div class="header">
    <div class="logo">
      <img :src="logo" alt="" />

      <div class="version">MES系统v1.0.0</div>
    </div>
    <div class="user-box">
      <!-- 搜索图标 -->
      <div
        :class="{ 'search-container': true, navActive: searchNavList?.length }"
      >
        <input
          class="search-input"
          placeholder="搜索页面"
          @input="search"
          @blur="onBlur"
          @focus="onFocus"
          v-model="searchVal"
          @keydown="onKeydown"
        />
        <button class="btn">
          <i
            class="ri-search-line"
            title="搜索"
            style="font-size: 18px; color: #fff"
          ></i>
        </button>
        <div class="search-list" v-if="filterNavList.length">
          <ul>
            <li
              v-for="item in filterNavList"
              :key="item.name"
              :class="{ 'active-item': item.meta.title == isActiveItem }"
            >
              <!-- 图标 -->
              <i :class="item.meta.icon" v-if="item.meta.icon"></i>
              {{ item.meta.title }}
            </li>
          </ul>
        </div>
      </div>
      <i
        class="ri-line-chart-line icon-hover"
        title="数据大屏"
        @click="goToPage('/chartHome')"
        style="margin-right: 20px; font-size: 18px"
      ></i>
      <!-- 通知图标 -->
      <i
        class="ri-notification-3-line icon-hover notice-num"
        title="通知"
        @click="goToPage('/notify')"
        style="margin-right: 20px; font-size: 18px"
      ></i>
      <!-- 全屏 -->
      <i
        class="ri-fullscreen-line icon-hover"
        title="全屏"
        @click="fullScreen"
        style="margin-right: 20px; font-size: 18px"
      ></i>
	  <!-- 锁屏组件 -->
	  <g-lock-screen/>
      <div class="avatar">
        <img src="@/assets/logo.png" alt="" srcset="" />
      </div>
      <a-dropdown>
        <a style="color: #333" @click="(e) => e.preventDefault()">
          admin <a-icon type="down" />
        </a>
        <a-menu slot="overlay">
          <a-menu-item>
            <a href="javascript:;" @click="openSetting">设置</a>
          </a-menu-item>
          <a-menu-item>
            <a href="javascript:;" @click="quit">退出系统</a>
          </a-menu-item>
        </a-menu>
      </a-dropdown>
    </div>
  </div>
</template>
<script>
import store from "@/store";
import init from "./search";
import { AppUtils, createModal } from "@/utils";
import Setting from "@/layout/header/setting/setting.vue";

import gLockScreen from '@/components/g-lock-screen/g-lock-screen.vue';

export default {
  components: {
	gLockScreen
  },
  data() {
    return {
      filterNavList: [],
      searchVal: "",
      isActiveItem: "", // 当前激活项
      isActiveItems: {}, // 当前激活项
      logo: require("@/assets/logo.png"),
    };
  },
  computed: {
    searchNavList() {
      const flattenedRoutes = this.flattenRoutes(store.state.app.routers);
      return flattenedRoutes;
    },
  },
  methods: {
    flattenRoutes(routes, parentPath = "") {
      let flattenedRoutes = [];
      routes.forEach((route) => {
        // If the current object does not have the children property, add it to the result array
        if (!route.children) {
          route.parentPath = parentPath;
          flattenedRoutes.push(route);
        } else {
          // If the current object has the children property, recursively call the flattenRoutes function to process the children array
          const updatedParentPath =
            parentPath + (parentPath != "" ? "/" + route.path : route.path);
          const children = this.flattenRoutes(
            route.children,
            updatedParentPath
          );
          flattenedRoutes = flattenedRoutes.concat(children);
        }
      });

      return flattenedRoutes;
    },
    quit() {
      this.$message.info("退出登录");
      setTimeout(() => {
        // 清楚激活tabactive和tabslist
        store.dispatch("app/clearTabs");
        localStorage.setItem("user", "");
        localStorage.setItem("Admin-Token", "");
        this.$router.push("/login");
      }, 200);
    },
    goToPage(path) {
      this.$router.push(path);
    },
    search(e) {
      const value = e.target.value;
      // value为空，清空搜索列表
      if (!value) {
        this.filterNavList = [];
        this.isActiveItem = "";
        return;
      }
      this.filterNavList = this.searchNavList.filter((item) => {
        return item.meta.title.includes(value);
      });
      // 如果有length
      if (this.filterNavList?.length) {
        this.isActiveItem = this.filterNavList[0].meta.title;
        this.isActiveItems = this.filterNavList[0];
      }
    },
    onBlur() {
      this.filterNavList = [];
      this.searchVal = "";
      this.isActiveItems = {};
      this.isActiveItem = "";
      document.removeEventListener("keydown", () => {});
    },
    onKeydown(e) {
      if (e.keyCode === 40 || e.keyCode == 38) {
        // 阻止
        e.preventDefault();
      }
    },
    /**
     * Handle keydown event
     * @param {KeyboardEvent} event - The keydown event
     */
    handleKeyDown(event) {
      const keyCode = event.keyCode || event.which;
      const key = event.key;

      // Check if filterNavList is not empty
      if (this.filterNavList?.length) {
        if (keyCode === 40) {
          // Move to the next item
          this.moveNextItem();
        } else if (keyCode === 38) {
          // Move to the previous item
          this.movePreviousItem();
        } else if (keyCode === 13) {
          // Press enter key
          this.goToActiveItem();
        }
      }
    },
    /**
     * Move to the next item in filterNavList
     */
    moveNextItem() {
      const index = this.filterNavList.findIndex(
        (item) => item.meta.title == this.isActiveItem
      );
      if (index === this.filterNavList.length - 1) {
        this.isActiveItem = this.filterNavList[0].meta.title;
        this.isActiveItems = this.filterNavList[0];
      } else {
        this.isActiveItem = this.filterNavList[index + 1].meta.title;
        this.isActiveItems = this.filterNavList[index + 1];
      }
    },
    /**
     * Move to the previous item in filterNavList
     */
    movePreviousItem() {
      const index = this.filterNavList.findIndex(
        (item) => item.meta.title === this.isActiveItem
      );
      if (index === 0) {
        this.isActiveItem =
          this.filterNavList[this.filterNavList.length - 1].meta.title;
        this.isActiveItems = this.filterNavList[this.filterNavList.length - 1];
      } else {
        this.isActiveItem = this.filterNavList[index - 1].meta.title;
        this.isActiveItems = this.filterNavList[index - 1];
      }
    },
    /**
     * Go to the active item
     */
    goToActiveItem() {
      console.log(this.isActiveItems);
      AppUtils.goToPage(
        this.isActiveItems?.parentPath
          ? this.isActiveItems?.parentPath + "/" + this.isActiveItems.path
          : this.isActiveItems.path
      );
      // 主动input失去焦点
      let _input = document.querySelector(".search-input");
      _input.blur();
    },
    onFocus() {
      // Add event listener for keydown event
      document.addEventListener("keydown", this.handleKeyDown);
    },
    fullScreen() {
      AppUtils.fullScreen();
    },
    openSetting() {
      createModal(
        {
          title: "设置",
          width: "600px",
        },
        {
          component: Setting,
        }
      );
    },
  },
  mounted() {
    // Call init function after the component is rendered
    this.$nextTick(() => {
      init();
    });

    // 获取本地存储 imageData
    const imageData = localStorage.getItem("imageData");
    if (imageData) {
      this.logo = imageData;
    }
  },

  // 销毁
  beforeDestroy() {},
};
</script>

<style lang="less">
.header {
  flex-basis: 50px;
  background: #fff;
  flex-shrink: 0;
  display: flex;
  justify-content: space-between;
  padding: 10px 24px;
  align-items: center;
  border-bottom: 1px solid #f0f0f0;
}
.logo {
  display: flex;
  align-items: center;
  .version {
    margin-left: 10px;
    color: #333;
    font-size: 20px;
    font-weight: 500;
    font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  }
  img {
    width: 150px;
    height: 39px;
  }
}

.avatar {
  border-radius: 50%;
  width: 30px;
  height: 30px;
  overflow: hidden;
  margin-right: 10px;
  img {
    width: 100%;
  }
}

.user-box {
  display: flex;
  align-items: center;
  color: #333;
}

.icon-hover {
  cursor: pointer;
  transition: all 0.3s;
  color: #808080;
  &:hover {
    color: #000;
  }
}

.notice-num {
  position: relative;
  &::after {
    content: "";
    position: absolute;
    top: 0px;
    right: -5px;
    width: 8px;
    height: 8px;
    line-height: 5px;
    text-align: center;
    border-radius: 50%;
    background-color: red;
    color: #333;
    font-size: 12px;
    font-weight: bold;
    display: inline-block;
  }
}

.search-container {
  position: relative;
  height: 35px;
  margin-right: 20px;
  /* 四角阴影 */
}

.search-input {
  height: 100%;
  width: 35px;
  border: 0;
  padding: 15px;
  box-sizing: border-box;
  font-size: 14px;
  background-color: @primary-color;
  transition: width 0.5s ease;
  border-radius: 15px;
  color: #fff;
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.4);
}

.search-input::placeholder {
  color: #d4d3d3; /* 设置你想要的字体颜色 */
  font-size: 14px;
}

.btn {
  position: absolute;
  inset: auto 0 0 auto;
  width: 35px;
  height: 100%;
  cursor: pointer;
  font-size: 12px;
  border: 0;
  background-color: @primary-color;
  transition: transform 0.5s ease;
  border-radius: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.btn:focus,
.search-input:focus {
  outline: none;
}

.search-container.active .search-input {
  width: 200px;
}

.search-container.navActive .search-list {
  width: 100%;
	height: auto;
  display: block;
}

.search-list {
  display: none;
  position: absolute;
  top: 38px;
  width: 0;
  height: 0;
  background-color: #fff;
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.3);
  border-radius: 0 0 8px;
  z-index: 999;
  ul {
    margin: 0;
    padding: 0;
    li {
      padding: 5px 10px;
      box-sizing: border-box;
      border-bottom: 1px solid #f0f0f0;
      cursor: pointer;
      list-style: none;
      &:last-child {
        border-bottom: none;
      }
      &:hover {
        background-color: #f0f0f0;
      }
    }
  }
}

.active-item {
  background-color: #f0f0f0;
}

.search-container.active .btn {
  /* transform: translateX(140px); */
}
</style>
