

function _deliverTimeOption() {
  const option = {
    legend: {
      data: ["应完成订单", "实际完成订单","准时交付率"],
      textStyle: {
        color: "#8cb9d5", // 设置图例文字颜色为红色
      },
    },
    grid:{
      bottom:20
    },
    xAxis: {
      type: "category",
      data: ["1月", "2月", "3月", "4月"],

      splitLine: { show: true },
      axisLabel: {
        textStyle: { color: "#8cb9d5" }, //字体颜色
      },
      axisTick: { show: false }, //尺寸边线

      axisLine: {
        show: false,
        lineStyle: {
          type: "solid",
          color: "#3B526C", //下边线的颜色
          width: "1", //坐标线的宽度
        },
      },
      splitLine: {
        show: false, //垂直线
        lineStyle: {
          color: "#3B526C",
        },
      },
    },
    yAxis: [{
      type: "value",
      axisTick: { show: false }, //尺寸边线
      splitLine: { 
        show: true,
        lineStyle: {
        color: "#0f1e55",
      }, },
      axisLabel: {
        textStyle: { color: "#C6CACD" }, //字体颜色
        
      },
      axisLine: {
        lineStyle: {
          type: "solid",
          color: "#8cb9d5", //下边线的颜色
          width: "1", //坐标线的宽度
        },
      },
    },
    {
      type: 'value',
      min: 80,
      max: 100,
      interval: 5,
      axisTick: { show: false }, //尺寸边线
      splitLine: { show: false },
      axisLine: {
        lineStyle: {
          type: "solid",
          color: "#8cb9d5", //下边线的颜色
          width: "1", //坐标线的宽度
        },
      },
      axisLabel: {
        formatter: '{value} %',
        textStyle: { color: "#C6CACD" }, //字体颜色

      }
    }
  ],
    tooltip: {
      trigger: "axis",
    },
    series: [
      {
        barWidth: "10px",
        label: {
          show: false,
        },
        name: "应完成订单",
        type: "bar",
        data: [18, 23, 25, 55],
        itemStyle: {
          borderRadius: [5, 5, 0, 0]
        },
        color:['#0459d2']

      },
      {
        barWidth: "10px",
        label: {
          show: false,
        },
        name: "实际完成订单",
        type: "bar",
        data: [10, 20, 60, 80],
        itemStyle: {
          borderRadius: [5, 5, 0, 0]
        },
        color: ['#3896ba']

      },
      {
        label: {
          show: false,
        },
        name: "准时交付率",
        type: "line",
        data: [10, 20, 25, 98],
        smooth: true,
        
      },
    ],
  };
  return option;
};

const deliverTimeOption = _deliverTimeOption();

export default deliverTimeOption;