<template>
  <a-table
    :columns="columns"
    :data-source="dataList"
    :pagination="false"
    :scroll="{ y: scrollY }"
    :loading="isTableLoading"
    class="ndo-table"
    id="ndo-table"
    @change="onChange"
    :rowKey="(record) => record.id"
  >
    <!-- 用于设置表头的提示信息 -->
    <span slot="customTitle"
      >{{ handName }}
      <span class="pageSize-box">{{ dataList?.length }}/{{ total }}</span>
    </span>
    <template slot="name" slot-scope="text, record">
      <div
        class="row-td ellsText"
        @click="rowClicked(record)"
        :class="{ 'row-checked': entityId == record.id }"
        style="width: 100%"
      >
        {{ text }}
      </div>
    </template>
  </a-table>
</template>

<script>
import { getTableScroll } from "@/utils/getTableScroll";

export default {
  name: "GNdoTable",
  props: {
    // 表头名称
    handName: {
      type: String,
      default: "",
    },
    // 数据
    dataList: {
      type: Array,
      default: () => {
        return [];
      },
    },
    // 是否加载
    isTableLoading: {
      type: Boolean,
      default: false,
    },
    // 数据条数
    total: {
      type: Number,
      default: 0,
    },
    id: {
      type: String | Number,
    },
  },
  data() {
    return {
      columns: [
        {
          dataIndex: "name",
          sorter: true,
          width: "20%",
          key: "name",
          ellipsis: true,
          scopedSlots: { customRender: "name" },
          slots: { title: "customTitle" },
        },
      ],
      scrollY: 100,
      entityId: null,
    };
  },
  watch: {
    id(v) {
      this.entityId = v;
      this.setBackGround();
    },
  },

  mounted() {
    this.$nextTick(() => {
      this.scrollY = getTableScroll({ id: "ndo-table" }, 48);
    });
  },

  methods: {
    onChange(e) {
      console.log("onChange", e);
    },
    rowClicked(record) {
      this.entityId = record.id;
      const temp = { id: record.id, name: record.name };

      this.$emit("rowClick", temp);
      this.setBackGround();
    },
    setBackGround() {},
  },
};
</script>

<style lang="less" scoped>
.ndo-table {
  overflow-y: auto;
}
:deep(.ant-table-placeholder) {
  border-bottom: none;
}

:deep(.ant-table-row) {
  cursor: pointer;
}

:deep(.ant-table-tbody > tr > td) {
  padding: 0;
}

.row-checked {
  color: @primary-color;
  background: #ebf2fa;
  font-size: 14px;
  font-weight: bold;
}

.row-td {
  padding: 11px 24px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.tit-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
  .pageSize-box {
    display: inline-block;
    padding: 2px 8px;
    background-color: #f2f2f2;
  }
}
</style>
