import store from "@/store/app";
import Redirect from '@/layout/redirect/redirect.vue'
/**
 * Filter asynchronous routing tables by recursion
 * @param routes asyncRoutes
 * @param roles
 */
 export function filterAsyncRoutes(routes, roles) {
	const res = []
  
	routes.forEach((route) => {
	  const tmp = { ...route }
	  
	  if (hasPermission(roles, tmp)) {
		if (tmp.children) {
		  tmp.children = filterAsyncRoutes(tmp.children, roles)
		}
		res.push(tmp)
	  }
	})
  
	return res
  }

  /**
 * Use meta.role to determine if the current user has permission
 * @param roles
 * @param route
 */
function hasPermission(roles, route) {
	if (route.meta && route.meta.roles) {
	  return roles.some((role) => route.meta.roles.includes(role))
	} else {
	  return true
	}
}

/**
 * @param button-roles
 */

export function hasBtnPermission(permission){
	const myBtns = store.state.app.buttons;
	return myBtns.indexOf(permission) > -1
}

/**
 * 匹配本地文件目录路由
 * @param {Array} routes - 路由数组
 * @param {string} parentPath - 父路径
 * @param {number} level - 层级，默认为0
 * @returns {Array} - 生成的路由数组
 */
export function generateRouters(routes, parentPath = '', level = 0) {
	routes.forEach((item) => {
	  let path // 匹配文件路径
	  if (level === 0) { // 第一层路由
		path = item.path
		if(!item.children){
			item.component = () => import(`@/views${path}${path}.vue`);
		} else {
			item.component = Redirect;
		}
	  } else { // 子路由
		
		// path = `/${item.path}`
		if(!item.children){
			path = parentPath + `${'/'+item.path+'/'+item.path}`
			item.component = () => import(`@/views${path}.vue`);
		} else {
			item.component = Redirect;
			path = parentPath+`/${item.path}`
		}
	  }
	  if (item.children && item.children.length > 0) {
		item.children = generateRouters(item.children, path, level + 1);
	  }
	})
	return routes
  }