<template>
    <div>
        <a-select
            :value="pageIndex"
            @change="onPageChange"
            class="select-box"
        >
            <a-select-option
                v-for="(item, index) in pageNum"
                :key="index"
                :value="item"
            >
                {{ item }}
            </a-select-option>
        </a-select>
    </div>
</template>

<script>
export default {
    name: 'PageSelect',
    props: {
        pageIndex: {
            type: Number,
            default: 0
        },
        pageNum: {
            type: Array,
            default: () => []
        }
    },
    data() {
        return {
        };
    },

    mounted() {
    },

    methods: {
        onPageChange(e){
            this.$emit('onPageChange', e)
        }
    },
};
</script>

<style lang="scss" scoped>
.select-box {
    width: 70px;
}
</style>