<template>
  <div class="flex-layout">
    <!-- 按钮组插槽 -->
    <div class="g-btn-group">
      <slot name="btnGroup"></slot>
    </div>
    <div class="flex-content">
      <!-- 左侧列表 -->
      <div class="left-body">
        <!-- 搜索组件 -->
        <div class="suffix-search">
          <g-suffix-search
            :searchVal.sync="searchVal"
            @onSearch="suffixSearch"
            @onSearchValChange="onValChange"
          ></g-suffix-search>
        </div>
        <div class="scroll-table">
          <div class="scroll-box">
            <!-- 带滚动条表格显示区 -->
            <slot name="table"></slot>
          </div>
        </div>
        <!-- 分页 -->
        <div class="pagination">
          <!-- 上一页按钮 -->
          <div>
            <i
              class="ri-arrow-left-s-line icon-left"
              style="font-size: 22px; cursor: pointer"
              @click="toLeftPageStart"
            ></i>
          </div>
          <g-btn
            text="上一页"
            type="default"
            :disabled="pageIndex == 1"
            @callback="prevPage"
          ></g-btn>

          <page-select
            :pageNum="pageNum"
            @onPageChange="onPageChange"
            :pageIndex="pageIndex"
          >
          </page-select>

          <!-- 下一页按钮 -->
          <g-btn
            text="下一页"
            type="default"
            :disabled="pageIndex == pageNum.length || pageNum.length <= 1"
            @callback="nextPage"
          ></g-btn>
          <!-- 右箭头 -->
          <div>
            <i
              class="ri-arrow-right-s-line icon-right"
              style="font-size: 22px; cursor: pointer"
              @click="toRightPageEnd"
            ></i>
          </div>
        </div>
      </div>
      <!-- 右边 -->
      <div class="right-body">
        <div class="detail-box">
          <slot name="content"></slot>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import PageSelect from "./page-select/page-select.vue";
import GSuffixSearch from "../g-suffix-search/g-suffix-search.vue";
export default {
  name: "GBodyLayout",
  props: {
    pageIndex: {
      default: 1,
    },
    pageSize: {
      default: 10,
    },
    total: {
      default: 0,
    },
    tableHeader: {
      default: "",
    },
    filterText: {
      type: String,
    },
  },
  components: {
    PageSelect,
    GSuffixSearch,
  },

  data() {
    return {
      tableIndex: 1, // 当前页码
      searchVal: this.filterText, // 搜索值
    };
  },
  computed: {
    pageNum() {
      const maxIndex = Math.ceil(Number(this.total) / 25);
      // 填充一个 1-maxIndex的数组
      return Array.from({ length: maxIndex }, (v, k) => k + 1);
    },
  },

  mounted() {},

  methods: {
    onValChange(e) {
      this.$emit("update:filterText", e);
    },
    prevPage() {
      this.$emit("pageIndexChange", this.pageIndex - 1);
    },

    nextPage() {
      this.$emit("pageIndexChange", this.pageIndex + 1);
    },
    onPageChange(e) {
      console.log("onPageChange", e);
      this.$emit("pageIndexChange", e);
    },
    // 第一页
    toLeftPageStart() {
      if (this.pageNum.length <= 1) {
        return;
      }
      this.$emit("pageIndexChange", 1);
    },
    // 最后一页
    toRightPageEnd() {
      if (this.pageNum.length <= 1) {
        return;
      }
      this.$emit("pageIndexChange", this.pageNum.length);
    },
    // 搜索
    suffixSearch(query) {
      this.$emit("suffixSearchCallback", query);
    },
  },
};
</script>

<style lang="scss" scoped>
.flex-layout {
  flex: 1;
  display: flex;
  width: 100%;
  height: 100%;
  overflow-y: auto;
  flex-direction: column;
}

.g-btn-group {
  margin-bottom: 8px;
}

.flex-content {
  display: flex;
  flex: 1;
  overflow-y: auto;
}

.left-body {
  width: 250px;
  margin-right: 8px;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  .suffix-search {
    padding: 4px 8px;
  }

  .scroll-table {
    flex: 1;
    display: flex;
    flex-direction: column;
    overflow-y: hidden;
    .table-header {
      font-size: 16px;
      padding: 10px 24px;
      border-bottom: 1px solid #eee;
    }
    .scroll-box {
      flex: 1;
      font-size: 14px;
    }
  }

  .pagination {
    box-sizing: border-box;
    text-align: center;
    display: flex;
    font-size: 14px;
    justify-content: space-around;
    padding-bottom: 8px;
    button {
      font-size: 12px;
      padding: 0 8px;
    }
  }
}

.right-body {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  box-sizing: border-box;

  .detail-box {
    flex: 1;
    display: flex;
    flex-direction: column;
    overflow-y: auto;
  }
}
</style>
