<template>
    <div ref="animeContainer" class="anime-wrapper">
      <slot></slot>
    </div>
  </template>
  
  <script>
  import anime from 'animejs';
  
  export default {
    name: 'AnimeWrapper',
    props: {
      // 动画配置，直接传递给anime.js
      config: {
        type: Object,
        required: true
      },
      // 是否自动播放动画
      autoplay: {
        type: Boolean,
        default: true
      },
      // 动画延迟时间(ms)
      delay: {
        type: Number,
        default: 0
      },
      // 动画触发方式：'mounted', 'visible', 'manual'
      trigger: {
        type: String,
        default: 'mounted',
        validator: (value) => ['mounted', 'visible', 'manual'].includes(value)
      }
    },
    data() {
      return {
        animation: null,
        observer: null,
        isVisible: false
      };
    },
    mounted() {
      console.log('组件已挂载，触发模式:', this.trigger, '自动播放:', this.autoplay);
      
      if (this.trigger === 'mounted' && this.autoplay) {
        console.log('准备执行挂载时动画');
        this.$nextTick(() => {
          this.startAnimation();
        });
      } else if (this.trigger === 'visible') {
        console.log('设置可见性触发器');
        this.setupIntersectionObserver();
      } else {
        console.log('手动触发模式，等待外部调用play()方法');
      }
    },
    methods: {
      setupIntersectionObserver() {
        // 创建交叉观察器，当元素进入视口时触发动画
        this.observer = new IntersectionObserver((entries) => {
          console.log('交叉观察器触发:', entries[0].isIntersecting);
          if (entries[0].isIntersecting && !this.isVisible) {
            this.isVisible = true;
            this.startAnimation();
            this.observer.disconnect();
          }
        }, { threshold: 0.1 });
        
        if (this.$refs.animeContainer) {
          this.observer.observe(this.$refs.animeContainer);
          console.log('已开始观察元素');
        } else {
          console.error('无法观察不存在的元素');
        }
      },
      startAnimation() {
        // 清除之前的动画实例
        if (this.animation) {
          this.animation.pause();
        }
        
        // 创建动画配置
        const config = {
          targets: this.$refs.animeContainer.querySelector('div'),
          ...this.config,
          delay: this.delay + (this.config.delay || 0)
        };
        
        // 创建动画实例
        this.animation = anime(config);
        
        // 触发动画开始事件
        this.$emit('animation-start', this.animation);
        
        // 监听动画完成
        this.animation.finished.then(() => {
          this.$emit('animation-end');
        });
      },
      pause() {
        if (this.animation) {
          this.animation.pause();
          this.$emit('animation-pause');
        }
      },
      play() {
        if (this.animation) {
          this.animation.play();
          this.$emit('animation-play');
        } else {
          this.startAnimation();
        }
      },
      restart() {
        if (this.animation) {
          this.animation.restart();
          this.$emit('animation-restart');
        } else {
          this.startAnimation();
        }
      },
      seek(time) {
        if (this.animation) {
          this.animation.seek(time);
        }
      }
    },
    beforeDestroy() {
      // 清理资源
      if (this.animation) {
        this.animation.pause();
        this.animation = null;
      }
      
      if (this.observer) {
        this.observer.disconnect();
        this.observer = null;
      }
    }
  };
  </script>
  
  <style scoped>
  .anime-wrapper {
    display: contents; /* 使包装器不影响DOM布局 */
  }
  </style>