<template>
  <a-form-model-item
    label="批次名称"
    prop="containerName"
    :rules="{
      required: true,
      message: '请输入批次名称',
      trigger: 'change',
    }"
  >
    <a-input
      placeholder="请输入批次名称"
      v-model="batchName"
      @change="inputChange"
    >
      <template #suffix>
        <a-popover
          v-model:visible="visible"
          trigger="click"
          placement="bottomRight"
        >
          <template #content>
            <div class="batch-table">
              <div class="batch-search">
                <a-input
                  placeholder="请输入批次名称"
                  name="containerName"
                  class="batch-input"
                  v-model="containerName"
                />

                <g-btn text="查询" @callback="refresh(true)" type="primary">
                </g-btn>
                <i
                  class="ri-close-line"
                  style="float: right; cursor: pointer"
                  @click="
                    () => {
                      visible = false;
                    }
                  "
                ></i>
                <a-table
                  :columns="columns"
                  :data-source="dataList"
                  :scroll="{ x: `calc(100% + 1px)`, y: '200px' }"
                  :rowKey="(record) => record.id"
                  :pagination="pagination"
                  @change="tableChange"
                  :customRow="handleRowClick"
                ></a-table>
              </div>
            </div>
          </template>
          <!-- 列表图标 -->
          <i class="ri-arrow-down-s-line"></i>
        </a-popover>
      </template>
    </a-input>
  </a-form-model-item>
</template>

<script>
import { tableBase } from "@/utils";
export default {
  name: "GGinMindSelect",
  mixins: [tableBase],
  props: {
    value: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      visible: false,
      columns: [
        {
          title: "批次名称",
          dataIndex: "batchName",
          key: "batchName",
        },
      ],
      containerName: "",
      batchName: this.value,
    };
  },
  watch: {
    value(val) {
      this.batchName = val;
    },
  },

  mounted() {},

  methods: {
    fetchData(request) {
      // 手动循环生成数据 200条
      var _data = [];
      // 循环添加100数据
      for (let i = 0; i < 100; i++) {
        _data.push({
          id: i,
          batchName: "批次" + i,
        });
      }
      request.finishedCallback && request.finishedCallback();
      request.successCallback &&
        request.successCallback({ items: _data, totalCount: 100 });
    },
    inputChange() {
      this.$emit("change", this.batchName);
    },
    handleRowClick(record) {
      return {
        on: {
          click: () => {
            console.log(record);
            this.batchName = record.batchName;
            this.$emit("change", this.batchName);
            // 关闭
            this.visible = false;
          },
        },
      };
    },
  },
};
</script>

<style lang="scss" scoped>
.batch-input {
  width: 150px;
  margin-right: 8px;
  margin-bottom: 8px;
}

.batch-table {
  width: 50vw;
}
</style>
