import Vuex from "vuex";
import settings from "@/utils/settings";
import skin from "@/utils/skin";
import router, { constantRoutes, asyncRoutes } from "@/router";
import { filterAsyncRoutes } from "@/utils/permission";
import Vue from "vue";
import routers from "@/mock/routes.js";

Vue.use(Vuex);

export default {
    namespaced: true,
    state: {
        counter: 0,
        isGetUserInfo: false,
        VITE_BASE_UI: "antd",
        addRoutes: [],
        routes: [],
        roles: [],
        buttons: [],
        caches: [],
        userInfo: null,
        routers: [],
        app: {
            tabActive: "",
            tabsList: [],
            activeRoute: "",
            refreshTag: [],
        },
        user: {
            keepAliveRoutes: [],
        },
        skinChoose: localStorage.getItem("skinChoose") ?
            skin[localStorage.getItem("skinChoose")] : skin[settings.SKIN_CHOOSE],
        colorPrimary: localStorage.getItem("colorPrimary") ?
            localStorage.getItem("colorPrimary") : settings.COLOR_PRIMARY,
    },
    mutations: {
        add(state) {
            state.counter++;
        },
        cur(state) {
            state.VITE_BASE_UI = "element3";
        },
        CHANGE_SETTING: (state, { key, value }) => {
            if (state.hasOwnProperty(key)) {
                state[key] = value;
                if (key === "skinChoose") {
                    localStorage.setItem("skinChoose", value.className);
                }
            }
        },
        COLOR_PRIMARY: (state, color) => {
            state.color = color;
        },
        M_routes: (state, routes) => {
            state.addRoutes = routes;
            // state.routes = constantRoutes.concat(routes); 暂时注释
            state.routes = routes;
        },
        M_roles: (state, roles) => {
            state.roles = roles;
        },
        SET_TABSLIST: (state, tabsList) => {
            state.app.tabsList = tabsList;
        },
        SET_TABACTIVE: (state, tabActive) => {
            state.app.tabActive = tabActive;
        },
        SET_ACTIVEROUTE: (state, activeRoute) => {
            state.app.activeRoute = activeRoute;
        },
        SET_BUTTONS: (state, authButtons) => {
            state.buttons = authButtons;
        },
        SET_REFRESH: (state, refreshTag) => {
            state.app.refreshTag = refreshTag;
        },
        SET_USERINFO(state, userInfo) {
            state.userInfo = userInfo;
        },
        SET_ROUTES: (state, routers) => {
            state.routers = routers;
        },
    },
    actions: {
        changeSetting({ commit }, data) {
            commit("CHANGE_SETTING", data);
        },
        setColorPrimary({ commit }, color) {
            localStorage.setItem("colorPrimary", color);
            commit("COLOR_PRIMARY", color);
        },
        generateRoutes({ commit }, roles) {
            return new Promise(async(resolve) => {
                let accessedRoutes;
                if (roles.includes("admin")) {
                    accessedRoutes = asyncRoutes[0].children || [];
                } else {
                    accessedRoutes = filterAsyncRoutes(asyncRoutes[0].children, roles);
                    console.log("过滤", accessedRoutes);
                }
                commit("M_routes", accessedRoutes);
                resolve(accessedRoutes);
            });
        },
        getInfo({ commit }) {
            return new Promise((resolve, reject) => {
                let roles = JSON.parse(localStorage.getItem("roles"));
                commit("M_roles", roles);
                resolve(roles);
            }).catch((error) => {
                reject(error);
            });
        },
        //初始化标签
        initTabs({ state, commit }) {
            let tabsList = JSON.parse(localStorage.getItem("tabsList"));
            let tabActive = localStorage.getItem("tabActive");

            if (tabsList && tabActive) {
                commit("SET_TABACTIVE", tabActive);
                commit("SET_TABSLIST", tabsList);
            }
        },
        // 清除标签
        clearTabs({ state, commit }) {
            localStorage.removeItem("tabsList");
            localStorage.removeItem("tabActive");
            commit("SET_TABSLIST", []);
            commit("SET_TABACTIVE", "");
        },
        //添加标签
        addTabs({ state, commit }, route) {
            let tabsList = JSON.parse(localStorage.getItem("tabsList"));
            if (!tabsList) {
                tabsList = [];
            }
            // let tabsList = state.app.tabsList;
            const isExists = tabsList.some((item) => item.fullPath == route.fullPath);
            if (!isExists) {
                tabsList.push(route);
            }
            commit("SET_TABACTIVE", route.fullPath);
            commit("SET_TABSLIST", tabsList);
            localStorage.setItem("tabsList", JSON.stringify(tabsList));
            localStorage.setItem("tabActive", route.fullPath);
            setTimeout(() => {
                tabsList = tabsList.map((item) => {
                    if (item.fullPath == route.fullPath) {
                        item["load"] = true;
                    }
                    return item;
                });

                commit("SET_TABSLIST", tabsList);
                localStorage.setItem("tabsList", JSON.stringify(tabsList));
            }, 0);
        },
        activeRoute({ commit }, path) {
            commit("SET_ACTIVEROUTE", path);
            // Cookies.set("activeRoute", path);
        },
        //点击标签切换选中
        clickTab({ state, commit }, index) {
            let tab = state.app.tabsList[index];
            commit("SET_TABACTIVE", tab.fullPath);
            localStorage.setItem("tabActive", tab.fullPath);
            router.push({ path: tab.fullPath });
        },
        // 关闭当前页
        closeCurrentTab({ state, commit }, obj) {
            let tabsList = state.app.tabsList;
            const index = tabsList.findIndex((item) => item.fullPath == obj.fullPath);
            tabsList.splice(index, 1);
            commit("SET_TABSLIST", tabsList);
            // 如果关闭后是最后一个 打开最后一个tab页面
            localStorage.setItem("tabsList", JSON.stringify(tabsList));
            if (obj.type == "current") {
                commit("SET_TABACTIVE", tabsList[tabsList.length - 1].fullPath);
                localStorage.setItem(
                    "tabActive",
                    tabsList[tabsList.length - 1].fullPath
                );

                router.push({ path: tabsList[tabsList.length - 1].fullPath });
            } else {
                console.log("关闭其他");
            }
        },
        // 关闭其他标签
        closeOtherTabs({ state, commit }, route) {
            let tabsList = state.app.tabsList;
            // 包含工作台
            tabsList = tabsList.filter(
                (item) => item.fullPath == route.fullPath || item.name == "dashboard"
            );
            commit("SET_TABSLIST", tabsList);
            localStorage.setItem("tabsList", JSON.stringify(tabsList));
        },

        // 按钮权限
        authButtonList({ commit }) {
            const buttonAuthList = JSON.parse(localStorage.getItem("buttonAuthList"));
            if (buttonAuthList) {
                commit("SET_BUTTONS", buttonAuthList);
            }
        },
        // addbtnauth
        addButtonAuth({ commit }, buttonAuth) {
            const buttonAuthList = [...this.state.buttons, buttonAuth];
            localStorage.setItem("buttonAuthList", JSON.stringify(buttonAuthList));
            commit("SET_BUTTONS", buttonAuthList);
        },
        // 添加缓存的路由组件
        addCache({ state, dispatch }, componentName) {
            const { caches } = state;
            if (!componentName || caches.includes(componentName)) return;
            caches.push(componentName);
        },
        // 移除缓存的路由组件
        removeCache({ state, dispatch }, componentName) {
            const { caches } = state;
            const index = caches.indexOf(componentName);
            if (index > -1) {
                return caches.splice(index, 1)[0];
            }
        },
        // 移除缓存的路由组件的实例
        async removeCacheEntry({ dispatch }, componentName) {
            const cacheRemoved = await dispatch("removeCache", componentName);
            if (cacheRemoved) {
                await Vue.nextTick();
                dispatch("addCache", componentName);
            }
        },
        getUserInfo({ commit }) {
            return new Promise((resolve) => {
                const data = {
                    name: "admin",
                    avatar: "",
                    roles: ["admin"],
                    introduction: "I am a super administrator",
                    status: 1,
                    email: "",
                    phone: "1234567890",
                    id: 1,
                    createTime: "2016-11-22 10:30:30",
                    lastLoginTime: "2019-05-27 10:30:30",
                    lastLoginIp: "127.0.0.1",
                    routers, // 路由信息数组
                };
                commit("SET_USERINFO", data); // 设置用户信息
                // commit("SET_ROLES", data.roles); // 设置角色权限
                // 延时模拟异步
                setTimeout(() => {
                    resolve(data);
                }, 500);
            });
        },
    },
};