

function _weekProductionOption() {
  const option = {
    legend: {
      data: ["任务量", "完成量","达成率"],
      textStyle: {
        color: "#8cb9d5", // 设置图例文字颜色为红色
      },
    },
    grid:{
      bottom:20
    },
    xAxis: {
      type: "category",
      data: ["Bom维护", "工艺", "图纸", "技术"],
      axisLabel: {
        textStyle: { color: "#8cb9d5" }, //字体颜色
      },

      axisLine: {
        show: true,
        lineStyle: {
          type: "solid",
          color: "#3B526C", //下边线的颜色
          width: "1", //坐标线的宽度
        },
      },
      splitLine: {
        show: false, //垂直线
        lineStyle: {
          color: "#8cb9d5",
        },
      },
    },
    yAxis: [{
      type: "value",
      axisTick: { show: false }, //尺寸边线
      splitLine: { show: true,
        lineStyle: {
          color: "#0f1e55",
        },
      
      },
      axisLabel: {
        textStyle: { color: "#8cb9d5" }, //字体颜色
      },
      axisLine: {
        
        lineStyle: {
          type: "solid",
          color: "#3B526C", //下边线的颜色
          width: "1", //坐标线的宽度
        },
      },
    },
    {
      type: 'value',
      min: 0,
      max: 120,
      interval: 20,
      axisTick: { show: false }, //尺寸边线
      splitLine: { show: false },
      axisLine: {
        
        lineStyle: {
          type: "solid",
          color: "#3B526C", //下边线的颜色
          width: "1", //坐标线的宽度
        },
      },
      axisLabel: {
        formatter: '{value} %',
        textStyle: { color: "#8cb9d5" }, //字体颜色

      }
    }
  ],
    tooltip: {
      trigger: "axis",
    },
    series: [
      {
        barWidth: "10px",
        label: {
          show: false,
        },
        name: "任务量",
        type: "bar",
        data: [18, 23, 29, 10],
        barGap: "-100%",
        color:['#3a81ff']
      },
      {
        barWidth: "10px",
        label: {
          show: false,
        },
        name: "完成量",
        type: "bar",
        data: [10, 20, 25, 5],
        barGap: "-100%",
        color:['#06b4cf']
      },
      {
        label: {
          show: false,
        },
        name: "达成率",
        type: "line",
        data: [10, 20, 25, 5],
        barGap: "-100%",
        smooth: true,
        color:['#5e846e']
      },
    ],
  };
  return option;
};

const weekProductionOption = _weekProductionOption();

export default weekProductionOption;