import GBtn from '@/components/g-btn/g-btn.vue';
// BaseChart组件
import BaseChart from '@/components/BaseChart.vue';
// g-blank 组件
import GBlank from '@/components/g-blank/g-blank.vue';
// g-btngroup组件
import GBtnGroup from '@/components/g-btn-group/g-btn-group.vue';
// g-body-layout组件
import GBodyLayout from '@/components/g-body-layout/g-body-layout.vue';
// g-ndo-table组件
import GNdoTable from '@/components/g-ndo-table/g-ndo-table.vue';
