/** 新增模块的全局组件注册函数 */
export default function setupComp(app) {
    // 注册全局组件
    app.component('GBtn', GBtn);
    app.component('BaseChart', BaseChart);
    app.component('GBtnGroup', GBtnGroup);
    app.component('GBodyLayout', GBodyLayout);
    app.component('GNdoTable', GNdoTable);
    app.component('GEmpty', GEmpty);
    app.component('GCollapse', GCollapse);
    app.component('GRdoTable', GRdoTable);
    app.component('GBodyFormLayout', GBodyFormLayout);
    app.component('GContainerList', GContainerList);
    app.component('GSelect', GSelect);
    app.component('GDataPoints', GDataPoints)
    app.component('GRdoSelect', GRdoSelect)
    app.component('GTableBar', GTableBar)
    app.component('GBlank', GBlank);
    app.component('GFileUpload', GFileUpload);
    app.component('GSign', GSign);
    app.component('GAnime', GAnime);
}