import axios from "axios";
import AppConst from "./utils/appConst";

export class AppLoad {
  // run方法
  static run(callback) {
    this.getAppConfig();
  }

  // 获取app基本配置
  static getAppConfig() {
      // 读取颜色
      const color = localStorage.getItem("color") || AppConst.defaultColor;
      if (color) {
        const el = document.documentElement;
        // 设置 --pc --mobile
        el.style.setProperty("--PC", color);
        window.less
          .modifyVars({
            "@primary-color": color,
            "@link-color": color,
            "@btn-primary-bg": color,
          })
          .then(() => {
            console.log("成功");
            // 存储
            localStorage.setItem("color", color);
          })
          .catch((error) => {
            alert("失败");
            console.log(error);
          });
      }

    // axios请求json
    axios.get("/assets/app.config.dev.json").then((res) => {
      AppConst.remoteServiceBaseUrl = res.data.serviceBaseUrl;
    });
  }
}
