import Vue from "vue";
import VueRouter from "vue-router";
import Layout from "../layout/home/home.vue";
import Login from "../layout/login/login.vue";
import notFound from "../layout/noFound/noFound.vue";
import chartHome from "../views/chartHome/chartHome.vue";
import chart1 from "../views/chartHome/chart1/chart1.vue";
import Redirect from "../layout/redirect/redirect.vue";

Vue.use(VueRouter);

export const constantRoutes = [
  {
    path: "/chartHome",
    name: "chartHome",
    component: chartHome,
    meta: { title: "大屏首页", icon: "ri-home-3-line", hidden: true },
  },
  {
    path: "/chart1",
    name: "chart1",
    meta: {
      title: "数据中心",
      icon: "dataCenter",
      hidden: true,
      isChart: true,
    },
    component: chart1,
  },
  {
    path: "/production",
    name: "production",
    meta: { title: "生产派单", icon: "product", hidden: true, isChart: true },
    component: () => import("@/views/chartHome/production/production.vue"),
  },
  {
    path: "/quality-control",
    name: "quality-control",
    meta: {
      title: "质量监控",
      icon: "quality-control",
      hidden: true,
      isChart: true,
    },
    component: () =>
      import("@/views/chartHome/quality-control/quality-control.vue"),
  },

  {
    path: "/login",
    name: "login",
    component: Login,
    meta: { title: "登录", icon: "el-icon-s-home", hidden: true },
  },
  // {
  //   // 这里不能直接使用vue-router3.x的*通配符做path了
  //   // vue-router4.x将匹配所有内容并将其放在 `$route.params.pathMatch` 下
  //   path: "/:pathMatch(.*)*",
  //   name: "404",
  //   meta: { title: "404", icon: "el-icon-s-tools", hidden: false },
  //   component: notFound,
  // },
  // {
  //   path: "/",
  //   name: "Home",
  //   component: Layout,
  //   hidden: true,
  //   redirect: "/dashboard",
  // }
];

export const asyncRoutes = [
  {
    path: "/",
    name: "Home",
    component: Layout,
    hidden: true,
    redirect: "/dashboard",
    children: [
      {
        path: "/dashboard",
        name: "dashboard",
        meta: { title: "工作台", icon: "ri-dashboard-line", hidden: false },
        component: () => import("@/views/dashboard/dashboard.vue"),
        isActive: false,
        order: 1,
      },
      

      
      {
        path: "/comp",
        name: "comp",
        component: () => import("@/views/comp/comp.vue"),
        meta: {
          title: "组件",
          icon: "ri-shield-user-line",
          hidden: false,
          roles: ["admin"],
        },
        order: 6,
      },
      {
        path: "/compRdo",
        name: "compRdo",
        component: () => import("@/views/compRdo/compRdo.vue"),
        meta: {
          title: "组件",
          icon: "ri-shield-user-line",
          hidden: false,
          roles: ["admin"],
        },
        order: 6,
      },
      {
        path: "/notify",
        name: "notify",
        component: () => import("@/views/notify/notify.vue"),
        meta: {
          title: "通知",
          icon: "ri-shield-user-line",
          hidden: true,
          roles: ["admin"],
        },
        order: 5,
      },
      
    ],
  },
];
const routes = [ ...constantRoutes];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});
const VueRouterPush = VueRouter.prototype.push;
VueRouter.prototype.push = function push(to) {
  return VueRouterPush.call(this, to).catch((err) => err);
};

export default router;
