<template>
  <div class="g-file-upload">
    <input type="file" @change="handleFileChange" ref="fileInput" style="display: none;" />
     <button type="button" @click="$refs.fileInput.click()" class="g-file-upload-button">
      <span v-if="fileName">{{ fileName }}</span>
      <span v-else>选择文件</span>
    </button>
    <button v-if="fileName" @click="clearFile" class="g-file-upload-clear-button">清除</button>
  </div>
</template>
<<script>


import { ref } from 'vue';

export default {
  name: 'GFileUpload',
  setup() {
    const fileName = ref('');

   

    const fileInput = ref(null); // 用于访问 input 元素

    const handleFileChange = (event) => {
      const file = event.target.files[0];
      if (file) {
        fileName.value = file.name;
      } else {
        fileName.value = '';
      }
    };

    const clearFile = () => {
      fileName.value = '';
      if (fileInput.value) {
        fileInput.value.value = ''; // 清空 input[type="file"] 的值
      }
    };

    return {
      fileName,
      handleFileChange,
      clearFile,
      fileInput, // 暴露 fileInput
    };
  },
};
</script>

<style scoped lang="less">
.g-file-upload {
  display: inline-flex;
  align-items: center;
  // padding: 10px; // 移除 padding，让按钮大小更紧凑
  // border: 1px dashed #ccc; // 移除边框
  // border-radius: 5px;
  // padding-right: 5px; /* 为按钮预留空间 */
}

.g-file-upload-button {
  .ant-btn span {
    margin-left: 8px; // 文件名与图标之间的间距
  }
}

.g-file-upload-clear-button {
   .ant-btn .ant-btn-link{
    padding: 0;
    margin-left: 8px;
    height: auto;
   }
}
</style>