import router from "@/router";
// import screenfull from "screenfull";
import fullscreen from "../fullscreen";
import AppConst from "../appConst";

// 导出 goToPage方法
const goToPage = (url) => {
    // 跳转
    router.push(url);
};

// 全屏
const fullScreen = () => {
    // screenfull.toggle();
    fullscreen.toggle();
};

const settingColor = (color) => {
    if (color == "default") {
        color = AppConst.defaultColor;
    }
    const el = document.documentElement;
    window.less
        .modifyVars({
            "@primary-color": color,
            "@link-color": color,
            "@btn-primary-bg": color,
        })
        .then(() => {
            console.log("成功");
            // 存储
            localStorage.setItem("color", color);
        })
        .catch((error) => {
            alert("失败");
            console.log(error);
        });

    // 设置 --pc --mobile
    el.style.setProperty("--PC", color);
};

export default {
    goToPage,
    fullScreen,
    settingColor,
};