import { defineComponent } from "vue";
const TableBase = defineComponent({
    name: "tableBase",
    data() {
        return {
            pageNumber: 1,
            pageSize: 10,
            pageSizeOptions: ["10", "20", "50", "100"],
            dataList: [],
            columns: [],
            virtualColumns: [],
            total: 0,
            selectRowKeys: [],
            isTableLoading: false,
            tableSize: "default",
        };
    },
    computed: {
        totalPage() {
            return Math.ceil(this.total / this.pageSize);
        },

        pagination() {
            return {
                current: this.pageNumber,
                total: this.total,
                pageSize: this.pageSize,
                showTotal: this.formatTotal,
                showSizeChanger: true,
                pageSizeOptions: this.pageSizeOptions,
            };
        },
        rowSelection() {
            return {
                selectedRowKeys: this.selectRowKeys,
                onChange: (selectedRowKeys) => {
                    this.selectRowKeys = selectedRowKeys;
                },
            };
        },
    },
    methods: {
        No(index) {
            return (index + 1) + (this.pageNumber - 1) * this.pageSize
        },
        formatTotal() {
            return `共 ${this.total} 条 
          第${this.pageNumber}页 / 共 ${this.totalPage}页
          `;
        },
        tableChange(e, filters, sorter) {
            console.log(e, filters, sorter);
            const { current, pageSize } = e;
            this.pageNumber = current;
            this.pageSize = pageSize;
        },
        // 获取表格数据
        fetchData() {},
        refresh() {
            console.log("refresh");
            this.dataList = [];
            this.fetchData(this.createFetchPage());
        },
        // 整理表格数据
        showItems(data, total) {
            this.dataList = data;
            this.total = total;
        },
        createFetchPage() {
            return {
                skipCount: (this.pageNumber - 1) * this.pageSize,
                maxResultCount: this.pageSize,
                sortConditions: [],
                sorting: "",
                finishedCallback: () => {
                    this.loading = false;
                    this.isTableLoading = false;
                },
                successCallback: (result) => {
                    if (!result || !result.items) {
                        this.dataList = [];
                        this.total = 0;
                    } else {
                        this.dataList = result.items;
                        this.total = result.totalCount;
                    }
                },
            };
        },
        /** 刷新表格数据并跳转到第一页（`pageNumber = 1`） */
        refreshGoFirstPage() {
            this.pageNumber = 1;
            this.restCheckStatus(this.dataList);
            this.refresh();
        },
        /**
         * 重置选中状态
         * @param entityList 数据集合
         */
        restCheckStatus() {
            this.selectedRowKeys = [];
        },
    },
    mounted() {
        console.log("tableBase mounted");
        this.refresh();
    },
});

export default TableBase;