      data: [820, 932, 901, 934, 1290, 1330, 1320],
      isScreenfull: false,
      //只要年月日 不要时分秒
      date: "",
      tableHeight: 100,
      weekProductionOption,
      deliverTimeOption,
      monthErrorOption,
      columns: [
