        },
        {
          name: "3",
          age: "JT-DBSADA549",
          address: "SFG",
          name2: "YZ154",
          age2: "2023-12-20",
          address2: "生产中",
        },
      ],
      columns2: [
        {
          title: "序号",
          dataIndex: "name",
          key: "name",
          width: 50,
        },
        {
          title: "班组",
          dataIndex: "age",
          key: "age",
          width: 100,
        },
        {
          title: "工单",
          dataIndex: "address",
          key: "address",
          width: 100,
        },
        {
          title: "物料代码",
          dataIndex: "name2",
          key: "name2",
          width: 100,
        },
        {
          title: "完成/工单",
          dataIndex: "age2",
          key: "age2",
          width: 100,
        },
        {
          title: "计划完成日期",
          dataIndex: "address2",
          key: "address2",
          width: 100,
        },
        {
          title: "生产进度",
          dataIndex: "status",
          key: "status",
          width: 100,
        },
      ],
      columns3: [
        {
          title: "序号",
          dataIndex: "name",
          key: "name",
          width: 100,
        },
        {
          title: "一级异常",
          dataIndex: "age",
          key: "age",
          width: 100,
        },
        {
