          width: 100,
        },
        {
          title: "异常状态",
          dataIndex: "age2",
          key: "age2",
          width: 100,
        },
      ],
      dateList2: [
        {
          name: "1",
          age: "金贵才",
          address: "ST74541sa5d12sa",
          name2: "MAXJ-07-0041",
          age2: "200/550",
