          address: "采购物料未到货",
          name2: "12/20",
          age2: "处理中",
        },
        {
          name: "3",
          age: "技术类",
          address: "技术图示有误",
          name2: "12/20",
          age2: "待确认",
        },
        {
          name: "4",
          age: "计划类",
          address: "计划排产异常",
          name2: "12/20",
          age2: "未处理",
        },
      ],
    };
  },
  computed: {
    // 折线图配置
    lineOption() {
      const option = {
        legend: {
          data: ["过程检", "产品检"],
          textStyle: {
            color: "#8cb9d5", // 设置图例文字颜色为红色
          },
        },

        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true,
        },
        tooltip: {
          trigger: "axis",
          valueFormatter: (value) => value + "%",
        },

        xAxis: {
          type: "category",
          data: ["6/1", "6/2", "6/3", "6/4", "6/5", "6/6", "6/7"],
          boundaryGap: false,
          axisTick: { show: false }, //尺寸边线

          axisLine: {
            show: false,
          },
          axisLabel: {
            textStyle: {
              color: "#8cb9d5", // 设置坐标轴标签文字颜色为蓝色
            },
          },
        },
        yAxis: {
          type: "value",
          axisTick: { show: false }, //尺寸边线
          splitLine: {
            show: true,
            lineStyle: {
              color: "#0f1e55",
            },
          },
          axisLabel: {
            formatter: "{value} %",
            textStyle: {
              color: "#8cb9d5", // 设置y轴标签文字颜色为红色
            },
          },
        },
        series: [
          {
            name: "过程检",
            type: "line",
            stack: "Total",
            data: [120, 132, 101, 134, 90, 230, 210],
            areaStyle: {
              //color: '#94C9EC'
              color: {
                type: "linear",
                x: 0,
                y: 0,
                x2: 0,
                y2: 1,
                colorStops: [
                  {
                    offset: 0,
                    color: "rgba(58,132,255, 0.5)", // 0% 处的颜色
                  },
                  {
                    offset: 1,
                    color: "rgba(58,132,255, 0)", // 100% 处的颜色
                  },
                ],
                global: false, // 缺省为 false
              },
            },
          },
          {
            name: "产品检",
            type: "line",
            stack: "Total",
            data: [220, 182, 191, 234, 290, 330, 310],
            areaStyle: {
              color: {
                type: "linear",
                x: 0,
                y: 0,
                x2: 0,
                y2: 1,
                colorStops: [
                  {
                    offset: 0,
                    color: "rgba(111,187,183, 0.5)", // 0% 处的颜色
                  },
                  {
                    offset: 1,
                    color: "rgba(111,187,183, 0)", // 100% 处的颜色
                  },
                ],
                global: false, // 缺省为 false
              },
            },
          },
        ],
      };
      return option;
    },
    // 柱状图配置
    barOption() {
      const option = {
        legend: {
          data: ["任务个数", "完成个数"],
          textStyle: {
            color: "#8cb9d5", // 设置图例文字颜色为红色
          },
        },
        grid: {
          bottom: 20,
          left: "15%",
        },
        xAxis: {
          type: "value",
          splitLine: { show: true },
          axisLabel: {
            textStyle: { color: "#8cb9d5" }, //字体颜色
          },
          axisLine: {
            //show: true,
            lineStyle: {
              type: "solid",
              color: "#3B526C", //下边线的颜色
              width: "1", //坐标线的宽度
            },
          },
          splitLine: {
            show: true, //垂直线
            lineStyle: {
              color: "#0f1e55",
            },
          },
        },
        yAxis: {
          type: "category",
          data: ["Bom维护", "工艺", "图纸", "技术"],
          axisTick: { show: false }, //尺寸边线
          splitLine: { show: false },
          axisLabel: {
            textStyle: { color: "#8cb9d5" }, //字体颜色
          },
          axisLine: {
            lineStyle: {
              type: "solid",
              color: "#3B526C", //下边线的颜色
              width: "1", //坐标线的宽度
            },
          },
        },
        tooltip: {
          trigger: "axis",
        },
        series: [
          {
            barWidth: "10px",
            label: {
              show: false,
            },
            name: "任务个数",
            type: "bar",
            data: [18, 23, 29, 10],
            barGap: "-100%",
            color: ["#3385f6"],
          },
          {
            barWidth: "10px",
            label: {
              show: false,
            },
            name: "完成个数",
            type: "bar",
            data: [10, 20, 25, 5],
            barGap: "-100%",
            color: ["#18b8a5"],
          },
        ],
      };
      return option;
    },
    // 饼图配置
    pieOption() {
      const option = {
        tooltip: {
          trigger: "item",
        },
        legend: {
          top: "5%",
          left: "center",
        },
        series: [
          {
            type: "pie",
            radius: ["40%", "70%"],
            avoidLabelOverlap: false,
            label: {
              show: false,
              position: "center",
            },
            emphasis: {
              label: {
                show: true,
                fontSize: 40,
                fontWeight: "bold",
              },
            },
            labelLine: {
              show: false,
            },
            data: this.type.map((item, index) => ({
              value: this.data[index],
              name: item,
            })),
          },
        ],
      };
      return option;
    },
  },
  mounted() {
    this.genData();
    // 开启一个定时器，数据变化后，图表也会更新
    setInterval(() => {
      this.data = this.genData();
    }, 3000);

    const currentDate = new Date();
    const year = currentDate.getFullYear();
    const month = String(currentDate.getMonth() + 1).padStart(2, "0");
    const day = String(currentDate.getDate()).padStart(2, "0");

    this.date = `${year}-${month}-${day}`;
    // setInterval(() => {
    //   this.date = new Date().toLocaleString();
    // }, 1000);

    this.$nextTick(() => {
      this.handleResize();
    });
    window.addEventListener("resize", this.handleResize);
  },
  beforeDestroy() {
    window.removeEventListener("resize", this.handleResize);
  },
  methods: {
    handleResize() {
      this.tableHeight =
        document.querySelector(".wrap-table").offsetHeight -
        document.querySelector(".ant-table-header").offsetHeight +
        20 +
        "px";
    },
    // 生成数据
    genData() {
      return this.type.map((item) => Math.ceil(Math.random() * 1000));
    },
    back() {
      if (this.isScreenfull) {
        this.toScreenfull();
      }
      this.$router.go(-1);
    },
    toScreenfull() {
      this.isScreenfull = !this.isScreenfull;
    //   screenfull.toggle();
    },
  },
};
</script>

<style lang="scss" scoped>
.chart {
  background: url(../../../assets/chartbg.png);
  display: flex;
  flex-direction: column;
  padding: 0 vw(45) 0;
  box-sizing: border-box;
  height: 100vh;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  .chart-one-item {
    background-color: #09184f;
    margin-right: vw(20);
    margin-bottom: vh(20);
    padding: vh(20) vw(20);
    box-sizing: border-box;
  }
  .chart-two-item {
    background-color: #09184f;
    margin-right: vw(20);
    margin-bottom: vh(20);
    padding: vh(20) vw(20);
    box-sizing: border-box;
  }
  .chart-one-item:nth-of-type(3n) {
    margin-right: 0;
  }
}

h4 {
  color: #71d0ec;
  text-align: center;
  line-height: 1;
  font-weight: bold;
  margin-bottom: 12px;
  font-size: fs(20);
}

.table-box {
  width: vw(520);
  height: vh(280);
  display: flex;
  flex-direction: column;
}

.wrap-table {
  width: 100%;
  flex: 1;
  overflow: hidden;
}

.table-box-main {
  width: vw(750);
  height: vh(280);
}

::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.1);
  border-radius: 0;
}

::-webkit-scrollbar {
  -webkit-appearance: none;
  width: 6px;
  height: 6px;
}

::-webkit-scrollbar-button {
  display: none;
}
::-webkit-scrollbar-corner {
  display: none;
}

::-webkit-scrollbar-thumb {
  cursor: pointer;
  border-radius: 5px;
  background: rgba(177, 177, 177, 0.15);
  transition: color 0.2s ease;
}

.wrap-chart {
  display: flex;
  justify-content: space-between;
  width: vw(770);
}

.order-box {
  margin-top: vh(30);
  border-top: #173471 2px solid;
  padding: vh(30) 0;
  box-sizing: border-box;
  display: flex;
  .order-num,
  .product-num {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex: 1;
  }

  .order-num {
    color: #b0e8f8;
  }

  span {
    font-size: fs(22);
    &:first-child {
      margin-top: vh(31);
      margin-bottom: vh(39);
    }
  }
  .product-num {
    color: #4c7ce4;
    font-weight: 500;
  }
}

.dateTime {
  font-size: fs(20);
}

.big-title {
  font-size: fs(24);
}

// 魔改表格样式
:deep(.ant-table) {
  border: none;
  color: #b0e8f8;
}

:deep(.ant-table-thead) {
  th {
    color: #b0e8f8 !important;
  }
}

:deep(.ant-table-row) {
  td {
    background-color: transparent !important;
    border: none;
    &:hover {
      background-color: transparent !important;
    }
  }
  border: none;

  &:hover {
    background-color: transparent !important;
  }
}

:deep(.ant-table-body) {
  background-color: #09184f !important;
  // &::-webkit-scrollbar-track {
  // background: transparent;
  // border-radius: 0;
  // }

  &::-webkit-scrollbar {
    -webkit-appearance: none;
    width: 6px;
    height: 6px;
  }

  &::-webkit-scrollbar-button {
    display: none;
  }
  &::-webkit-scrollbar-corner {
    display: none;
  }

  &::-webkit-scrollbar-thumb {
    cursor: pointer;
    border-radius: 5px;
    background: rgba(24, 173, 199, 1);
  }
}

:deep(.ant-table-small > .ant-table-content .ant-table-header) {
  background: #09184f !important;
  margin-bottom: -18px !important;
}

.bg-header {
  margin-top: vh(20);
  margin-bottom: vh(35);
}
</style>
