<template>
  <div class="chart">
    <div class="chart-box">
      <div
        style="
          display: flex;
          align-items: center;
          justify-content: center;
          align-items: center;
        "
      >
        <img src="@/assets/jmlogo.jpg" alt="" srcset="" style="width: 200px" />
        <h1>数据可视化大屏</h1>
        <div
          style="flex: 1; text-align: right;  font-weight: bold"
          class="dateTime"
        >
          {{ date }}----{{ week }}
        </div>
      </div>
      <div class="chart-items">
        <div
          v-for="(item, i) in charts"
          :key="i"
          class="chart-item"
          @click="targetChart(item)"
        >
          <img :src="item.meta.icon" alt="" srcset="" />

          <span>{{ item.meta.title }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import dataCenter from '@/assets/dataCenter.png';
import product from '@/assets/product.jpg';
import qualityControl from '@/assets/quality-control.png';
export default {
  name: "DataV2ChartHome",

  data() {
    return {
      charts: [], // 大屏路由
      date: new Date().toLocaleString(),
      week: "星期" + "日一二三四五六".charAt(new Date().getDay()),
      pngs:[dataCenter,product,qualityControl]
    };
  },

  mounted() {
    const _charts = this.$router.options.routes.filter(
      (item) => item?.meta?.isChart
    );
    this.charts = _charts.map((item,key) => {
      if (item.meta.icon) {
        item.meta.icon = this.pngs[key]
      }
      return item
    })

    setInterval(() => {
      // 格式化日期时间
      this.date = new Date();
      this.date = this.date.toLocaleString();
    }, 1000);

  },

  methods: {
    targetChart(item) {
      this.$router.push({
        path: item.path,
      });
    },
    
  },
};
</script>

<style lang="scss" scoped>
.chart {
  width: 100%;
  height: 100vh;
  box-sizing: border-box;
  // 背景颜色白深蓝渐变 从左到右倾斜
  background: linear-gradient(to right bottom, white 0%, white 10%, #3779c0);
  .chart-box {
    border-radius: 6px;
    height: 100%;
    padding: 20px;
    display: flex;
    flex-direction: column;
    h1 {
      text-align: center;
      color: #004ea2;
      margin-left: 10px;
    }
    .chart-items {
      margin-top: 10px;
      overflow: auto;
      flex: 1;
      display: flex;
      flex-wrap: wrap;
      box-sizing: border-box;
      padding: 24px;
      border-radius: 8px;
      box-shadow: 0 0 3px #004ea2;


      .chart-item {
        cursor: pointer;
        flex: 0 0 23.5%; // 关键代码
        margin-right: 2%; // 关键代码
        height: 260px;
        border: 1px solid #004ea2;
        margin-bottom: 24px;
        border-radius: 4px;
        position: relative;
        overflow: hidden;
        // 四角阴影
        box-shadow: 0 0 10px #004ea2;
        img {
          width: 100%;
          height: 100%;
          transition: all .5s;
          // hover放大
          &:hover {
            transform: scale(1.1);
          }
        }
        span {
          width: 100%;
          text-align: center;
          background-color: rgba(0, 78, 162,.6);
          color: #fff;
          padding: 12px 0;
          position: absolute;
          bottom: 0;
          left: 50%;
          transform: translate(-50%, );
        }
      }
      .chart-item:nth-of-type(4n) {
        margin-right: 0;
      }
    }
  }
}

.dateTime {
  // 炫酷字体
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  font-size: 26px;
}
</style>
