import { defineComponent } from "vue";
import { PageMode } from "./page";
import AppConst from "@/utils/appConst";
import TableBase from "./table-base";
import createModal from "@/utils/modal";

const GRdoPageBase = defineComponent({
  mixins: [TableBase],
  props: {},
  data() {
    return {
      saveTag: "", // 保存标记
      refreshTag: "", // 刷新标记
      resetTag: "", // 重置标记
      expendResetTag: "", // 展开重置标记
      filterText: "", // 过滤文本
      queryConditions: [], // 查询条件
      sortConditions: [], // 排序条件
      btnList: [], // 按钮列表
      rdoEditParams: {
        id: undefined,
        pageMode: undefined,
      }, // 编辑参数
      pageMode: "", // 页面模式
      id: "", // 编辑id
      isShowEdit: false, // 是否显示编辑
      lastRowClick: {}, // 最后一次点击行
      pageSizeChangeTag: false, // 分页大小改变标记
      pageSize: 25, // 分页大小
      loading: false,
      /** 当前选中数据是否是默认版本 */
      isRevOfRcdId: false,
      /** rdoBaseId */
      rdoBaseId: "",

      /** rdo id */
      rdoId: "",
      createModal,
      AppConst,
    };
  },
  created() {
    this.getBtnList(() => {});
  },
  mounted() {},
  methods: {
    refreshEditPanel() {},
    /** 按钮被点击 需要重写的抽象方法 */
    btnCallback(eventName) {},
    /** 删除 ndo 需要重写的抽象方法 */
    delete() {},
    /** 复制 dno 需要重写的抽象方法 */
    copy() {},
    getBtnList() {},
    /**
     * 新增或编辑DTO信息
     * @param id id
     */
    createOrEdit(rdoBaseId, rdoId) {
      this.isShowEdit = false;
      this.refreshTag = undefined;
      this.saveTag = undefined;
      this.resetTag = undefined;

      setTimeout(() => {
        this.isShowEdit = true;
        this.rdoBaseId = rdoBaseId;
        this.rdoId = rdoId;
        this.pageMode = !!rdoId ? PageMode.Edit : PageMode.Create;

        // 刷新按钮组
        this.getBtnList(() => {});
        // 如果当前输入值相等，那么不触发变更
        if (
          this.rdoEditParams &&
          this.rdoEditParams.rdoBaseId === rdoBaseId &&
          this.rdoEditParams.rdoId === rdoId
        ) {
          return;
        }
        // 构建编辑参数
        this.rdoEditParams = {
          rdoId: rdoId,
          rdoBaseId: rdoBaseId,
          pageMode: this.pageMode,
          isRevOfRcdId: this.isRevOfRcdId,
        };
      }, 0);
    },
    /** 表格单行点击事件 */
    rowClick(rowClick) {
      this.lastRowClick = rowClick;
      // 是否为默认版本
      this.isRevOfRcdId = rowClick.isRevOfRcdId;
      // 调用创建和编辑
      this.createOrEdit(rowClick.rdoBaseId, rowClick.rdoId);
      console.log("rowClick", rowClick, this.isRevOfRcdId);
    },
    /** 动态筛选条件发生改变 */
    onFilterChange(queryConditions) {
      this.queryConditions = queryConditions;
      this.refreshGoFirstPage();
    },
    /** 筛选条件初始化完成 */
    onFilterReady(queryConditions) {
      this.onFilterChange(queryConditions);
    },
    /** 模糊筛选条件发生改变  */
    filterTextChange(val) {
      this.filterText = val;
    },
    /** 编辑页面触发刷新 */
    childRefresh(params) {
      // 重置,如果编辑页面被重置了，那么需要清空所有
      this.reset(params.reset);
      // TODO: 这里操作 queryConditions 有问题，之后统一处理
      if (this.queryConditions && this.queryConditions.length > 1) {
        this.queryConditions[0].value = "";
        this.onFilterChange(this.queryConditions);
      }
      // 刷新
      this.refresh();
    },
    /** 页码发生改变，此处暂时兼容目前使用g-body-layout较多的位置 */
    pageIndexChange(pageIndex) {
      console.log(pageIndex);
      if (!this.pageSizeChangeTag) {
        this.pageNumber = pageIndex;
        this.tableExpendReset();
        this.refresh();
      } else {
        this.pageNumber = 1;
      }
      this.pageSizeChangeTag = false;
    },

    /** 每页显示数据量发生改变，此处暂时兼容目前使用g-body-layout较多的位置 */
    pageSizeChange(pageSize) {
      this.pageSizeChangeTag = true;
      if (this.pageNumber === 1) {
        this.pageSizeChangeTag = false;
      } else {
        this.pageNumber = 1;
      }
      this.pageSize = pageSize;
      this.tableExpendReset();
      this.refresh();
    },
    /** 更新表格展开数据重置标记，用于触发列表组件清空展开缓存 */
    tableExpendReset() {
      this.expendResetTag = Date.now().toString();
    },

    /** 重置页面数据 */
    reset(resetAll = false) {
      // 当目前是操作是创建时，置空数据
      if (resetAll) {
        this.rdoBaseId = undefined;
        this.rdoId = undefined;
        this.rdoEditParams = undefined;
        this.lastRowClick = undefined;
      }
      this.refreshTag = undefined;
      this.saveTag = undefined;
      this.resetTag = undefined;
      // 刷新按钮组
      this.getBtnList(() => {});
    },
    /** 按钮组被点击 */
    btnGroupClick(eventName) {
      // 判断当前对象是否有和事件同名的函数，如果有的话，自动调用
      const eventFunc = this[eventName];
      if (eventFunc) {
        eventFunc.apply(this);
        return;
      }

      // 默认的事件处理
      switch (eventName) {
        case AppConst.button.create:
          this.rdoEditParams = undefined;
          this.isRevOfRcdId = false;
          this.tableExpendReset();

          this.createOrEdit();
          break;
        case AppConst.button.createVersion:
          this.createOrEdit(this.rdoBaseId);
          break;
        case AppConst.button.deleteVersion:
          this.deleteRev();
          break;
        case AppConst.button.copyVersion:
          this.copyVersion();
          break;
        case AppConst.button.delete:
          this.pageNumber = 1;
          this.delete();
          break;
        case AppConst.button.refresh:
          this.refreshEditPanel();
          break;
        case AppConst.button.save:
          this.saveEditPanel();
          break;
        case AppConst.button.copy:
          this.copy();
          break;
        case AppConst.button.modelAudit:
          this.modelAudit();
          break;
      }

      // 没有的话调用此方法
      this.btnCallback(eventName);
    },
    /** 删除成功消息提示 */
    deleteSuccess() {
      const name = this.lastRowClick.name;

      this.$message.success(
        `删除成功-${name}-${this.$moment().format("YYYY/MM/DD HH:mm:ss")}`
      );

      // 隐藏编辑面板
      this.isShowEdit = false;

      // 重置
      this.reset(true);
      this.loading = true;
      this.isDelete = true;
    },

    /** 复制成功消息提示 */
    copySuccess(name, copyName) {
      this.$message.success(
        `复制成功-${copyName}-${this.$moment().format("YYYY/MM/DD HH:mm:ss")}`
      );
    },

    /** 更新save标记,以触发编辑组件中的save方法 */
    saveEditPanel() {
      this.saveTag = Date.now().toString();
    },

    /** 更新刷新标记,以触发编辑组件中的refresh方法 */
    refreshEditPanel() {
      this.refreshTag = Date.now().toString();
    },

    /** 更新重置标记，以触发编辑组件中的reset方法 */
    resetEditPanel() {
      this.resetTag = Date.now().toString();
    },
    // 搜索
    onFilterReady(text) {
      console.log("onFilterReady", text);
      this.queryConditions = text;
      this.refreshGoFirstPage();
    },
    /** 更新表格展开数据重置标记，用于触发列表组件清空展开缓存 */
    tableExpendReset() {
      this.expendResetTag = Date.now().toString();
    },
  },
});

export default GRdoPageBase;
