<template>
  <div class="modal">
    <div class="modal-content">
      <div>选择颜色: <g-color-picker v-model="color"></g-color-picker></div>
      <!-- 恢复默认 -->
      <div class="mt-1">
        <g-btn text="恢复默认" @callback="resetColor" type="primary"></g-btn>
      </div>
      <div class="mt-1">
        <!-- 系统logo -->
        <div class="mt-1">
          <div class="mt-1">系统logo</div>
          <div class="mt-1">
            <input
              type="file"
              id="file"
              ref="file"
              @change="handleFileUpload($event)"
              accept="image/*"
            />
            <img
              :src="url"
              alt=""
              style="width: 100px;"
              v-if="url"
            />
          </div>
        </div>
      </div>
    </div>
    <div class="modal-footer">
      <g-btn text="确认" @callback="save" type="primary"></g-btn>

      <g-btn text="取消" @callback="$emit('closeModal')"></g-btn>
    </div>
  </div>
</template>

<script>
import GColorPicker from "@/components/g-color-picker/g-color-picker.vue";
import { updateTheme } from "@/Theme/changeTheme";
import AppConst from "@/utils/appConst";
import { AppUtils } from "@/utils";

export default {
  name: "DataV2Setting",
  components: { GColorPicker },
  data() {
    return {
      color: "",
      AppUtils,
      url: "",
    };
  },
  watch: {
    color() {
      AppUtils.settingColor(this.color);
    },
  },

  mounted() {
    this.color = localStorage.getItem("color") || AppConst.defaultColor;
  },

  methods: {
    save() {
      this.$emit("closeModal");
    },
    resetColor() {
      this.color = AppConst.defaultColor;
      AppUtils.settingColor("default");
    },

    handleFileUpload(event) {
      this.file = this.$refs.file.files[0];

      // 将图片数据转换为Base64编码
      const reader = new FileReader();
      console.log(event);

      reader.onload = (event) => {
        const base64Data = event.target.result;

        localStorage.setItem("imageData", base64Data);

        // 从localStorage中获取文件数据
      this.url = localStorage.getItem("imageData");
      // 刷新浏览器
      location.reload();


      // const file = dataURLtoFile(base64Data, "image.jpg");
      };

      reader.readAsDataURL(this.file);
      
    },
    dataURLtoFile(dataURL, filename) {
      const arr = dataURL.split(",");
      const mime = arr[0].match(/:(.*?);/)[1];
      const bstr = atob(arr[1]);
      let n = bstr.length;
      const u8arr = new Uint8Array(n);

      while (n--) {
        u8arr[n] = bstr.charCodeAt(n);
      }

      return new File([u8arr], filename, { type: mime });
    },
  },
};
</script>

<style lang="less" scoped></style>
