<template>
  <a-row type="flex" :gutter="24" align="top">
    <a-col :span="4">
      <g-gin-mind-select :value.sync="newValue" @change="changeValue" />
    </a-col>
    <a-col :span="4">
      <a-form-model-item :label="'\u00a0'">
        <div style="display: flex">
          <g-btn
            text="查询"
            style="margin-right: 12px"
            type="primary"
            @callback="onSearch()"
            :disabled="!newValue"
          ></g-btn>
          <a-popover
            class="batchDetailBtn"
            trigger="click"
            v-model:visible="visiblePopover"
            placement="bottom"
          >
            <!-- title Tem -->
            <div @click="changePopover()">
              <i
                class="ri-information-line"
                style="font-size: 18px; cursor: pointer"
              ></i>
            </div>

            <template #title>
              <p style="margin: 0">批次详情</p>
            </template>
            <!-- content Tem -->
            <template #content> 666 </template>
          </a-popover>
        </div>
      </a-form-model-item>
    </a-col>
    <a-col :span="16" class="base-info">
      <g-container-basic-info> </g-container-basic-info>
    </a-col>
  </a-row>
</template>

<script>
import GGinMindSelect from "../g-ginMind-select/g-ginMind-select.vue";
import GContainerBasicInfo from "../g-container-basic-info/g-container-basic-info.vue";
export default {
  name: "GContainerList",
  components: {
    GGinMindSelect,
    GContainerBasicInfo,
  },
  props: {
    value: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      visiblePopover: false,
      newValue: "",
    };
  },

  mounted() {
    this.newValue = this.value;
  },

  methods: {
    onSearch() {},
    changePopover() {},
    changeValue(v) {
      this.newValue = v;
      this.$emit("update:value", v);
      this.$emit('change',v)
    },
  },
};
</script>

<style lang="scss" scoped>
:deep(.base-info) {
  height: 100%;

  .ant-row {
    height: 100%;
    align-items: center;
  }
}

:deep(.ant-form-item) {
  margin-bottom: 10px;
}
</style>
