import router, { asyncRoutes, constantRoutes } from "@/router";
import store from "./store";
import settings from "./settings";
import { getToken } from "@/utils/auth";
import NProgress from "nprogress";
NProgress.configure({ showSpinner: false }); // NProgress Configuration
import "nprogress/nprogress.css";
import getPageTitle from "@/utils/getPageTitle";
import { generateRouters } from "@/utils/permission";
import Layout from "@/layout/home/home.vue";


const NoLoginList = ["/clear"];
let isAddRouter = false;

const whiteList = ["/login"]; // no redirect whitelist
router.beforeEach(async (to, from, next) => {
  // start progress bar
  if (settings.isNeedNprogress) NProgress.start();
  // set page title
  document.title = getPageTitle(to.meta.title);
  /*
   * 总的来说：过滤动态路由
   * 1.是否与token 没有去登录页 ,有 如果要去登录页则重定向到首页。没有, 重新定向到登录页
   * 2.判断是否权限筛选,是,直接放行。没有，筛选动态路由后，添加动态路由然后放行，
   * */
  let hasToken = settings.isNeedLogin ? getToken() : "temp_token";
  if (hasToken) {

    if (to.path === "/login") {
      // if is logged in, redirect to the home page
      next("/");
    } else {
      //是否获取过用户信息

      let isGetUserInfo = store.state.app.isGetUserInfo;
      if (isGetUserInfo) {
        next();
      } else {
        if (!isAddRouter) {
          let accessRoutes = [];
          const roles = await store.dispatch("app/getInfo");

          //   accessRoutes = await store.dispatch("app/generateRoutes", roles);
          isAddRouter = true;
          //   accessRoutes.forEach((route) => {
          //     router.addRoute(route);
          //   });

          // 调用获取存储用户信息，并取得可访问的路由数组
          const { routers } = await store.dispatch("app/getUserInfo");
          const _routers = generateRouters(routers); // 匹配本地文件目录路由
          store.commit("app/SET_ROUTES", _routers); // 将路由表存入vuex
          // console.log(store.state.app.routers);
          // console.log(store.state.app.routes);
          // _routers.forEach((route) => {
          //   router.addRoute(route);
          // });
          // 动态添加路由
          router.addRoute({
              path: "/",
              name: "Home",
              component: Layout,
              hidden: true,
              redirect: "/dashboard",
              children: _routers
          });

          console.log(_routers);

      

          

          next({ ...to, replace: true });
          // next();
        } else {
          next();
        }
        // try {
        //     //let accessRoutes = [];
        //     if (!settings.isNeedLogin) {
        // 		console.log('ggg');
        //         // get user info
        //         // note: roles must be a object array! such as: ['admin'] or ,['developer','editor']
        //         // const { roles } = await store.dispatch("user/getInfo");
        //         // accessRoutes = await store.dispatch(
        //         //     "permission/generateRoutes",
        //         //     roles
        //         // );
        //     } else {
        //         // accessRoutes = asyncRoutes
        //         // store.commit('permission/M_routes', accessRoutes)
        //     }
        //     // dynamically add accessible routes
        //     //router4 addRoutes destroyed
        // 	// console.log(this.accessRoutes);
        //     // this.accessRoutes.forEach((route) => {
        //     //     router.addRoute(route);
        //     // });
        //     //already get userInfo
        //     //store.commit("permission/M_isGetUserInfo", true);
        //     // hack method to ensure that addRoutes is complete
        //     // set the replace: true, so the navigation will not leave a history record
        //     // next({ ...to, replace: true });
        // 	next()
        // } catch (err) {
        //     // await store.dispatch("user/resetToken");
        //     // next(`/login?redirect=${to.path}`);
        //     // if (settings.isNeedNprogress) NProgress.done();
        // }
      }
    }
  } else {
    //未登录，如果是免登录页面直接进入
    if (NoLoginList.indexOf(to.path) !== -1) {
      next();
    } else if (to.path === "/login") {
      //多加一步判断是否是去登录，以免进入路由死循环里
      next();
    } else {
      //非登录页，也不是免登录页就跳转到登录页
      // query: { redirect: to.fullPath }
      next({ path: "/login" });
    }
    // if (whiteList.indexOf(to.path) !== -1) {
    //     // next();
    // } else {
    //     // next(`/login?redirect=${to.path}`);
    //     if (settings.isNeedNprogress) NProgress.done();
    // }
  }
});

router.afterEach((to, from) => {
  //恢复原始keepalive
  //   store.dispatch("user/getKeepAlive");
  console.log("afterEach", to);
  if (settings.isNeedNprogress) NProgress.done();

  // 判断to.path有和constantRoutes数组里面相等的
  let res = constantRoutes.filter((item) => {
    return to.name == item.name;
  });
  if (to.meta && to.path != "/login" && !res.length) {
    store.dispatch("app/addTabs", {
      fullPath: to.fullPath,
      name: to.name,
      meta: to.meta,
    });
  }
  store.dispatch("app/activeRoute", to.fullPath);
  store.dispatch("app/authButtonList");
});
