import { defineComponent } from "vue";
import AppConst from "../appConst";

const GRdoPageEditBase = defineComponent({
  props: {
    /** rdo编辑信息, */
    rdoEditParams: { type: Object },
    /** save标记，用于触发编辑组件中的save方法 */
    saveTag: { type: String },
    /** 刷新标记，用于触发编辑组件中的refresh方法 */
    refreshTag: { type: String },
    /** 重置标记，用于触发编辑组建中的reset方法 */
    resetTag: { type: String },
  },
  data() {
    return {
      _ndoName: "",
      rdoId: "",
      rdoBaseId: "",
      operationName: undefined,
      operationTime: undefined,
      loading: false,
      isRevOfRcdId: false,
      rdo: {}


    };
  },
  mounted() {
    this.rdo = this.rdoEditParams;
  },
  watch: {
    saveTag(newVal) {
      if (newVal) {
        this.save();
      }
    },
    refreshTag(newVal) {
      if (newVal) {
        this.resetPageForms();
        this.refresh();
      }
    },
    resetTag(newVal) {
      if (newVal) {
        this.reset();
      }
    },
    rdo(newVal) {
      console.log('rdoEditParms发生变更', newVal);
      if (newVal) {
        this.rdoEditParamsChangeSelf(newVal);
      }
    }
  },
  computed: {
    
  },
  methods: {
    rdoEditParamsChangeSelf(input) {
      // 重置
      this.reset();

      // 空数据
      if (!input) {
        return;
      }

      // 填充数据
      this.rdoId = input.rdoId;
      this.rdoBaseId = input.rdoBaseId;
      this.isRevOfRcdId = input.isRevOfRcdId;

      // 调用初始化
      this.init();
    },
    /** 重置表单 */
    resetPageForms() {
      if (this.$refs.form) {
        try {
          this.$refs.form.clearValidate();
        } catch (e) {}
      }
    },
    /** 重置编辑面板数据 */
    refresh(showMsg = true) {
      // 重置表单数据
      this.resetFormData();
      // 重置表单
      this.resetPageForms();
      // 初始化
      this.init();

      if (showMsg) {
        this.refreshSuccess();
      }
    },
    /** 刷新成功消息提示 */
    refreshSuccess(name, version) {
      if (!name) {
        this.$message.success(AppConst.message.refreshSuccess);
        return;
      }
      if (this.rdoEditParams) {
        this.$message.success(
          `${AppConst.message.refreshSuccess}-${name}(${version})-${this.$moment().format(
            "YYYY/MM/DD HH:mm:ss"
          )}`
        );
        
      }
    },
    /** 保存成功消息提示 */
    saveSuccess(name) {
      this._ndoName = name;

      // 创建或更新
      let msg = AppConst.message.createSuccess;
      if (this.rdoId) {
        msg = AppConst.message.updateSuccess;
      }
      if (this.rdoEditParams) {
        this.$message.success(
          `${msg}-${name}-${this.$moment().format("YYYY/MM/DD HH:mm:ss")}`
        );
      }
    //   this.refresh(false);
    },
    /** 根据参数初始化组件数据 需要重写*/
    init() {},
    /** 重置表单绑定的数据源  抽象方法，需要子类重写*/
    resetFormData() {},
    /** 保存编辑面板数据  抽象方法，需要重写*/
    save() {},
    /** 重置 */
    reset() {
      // 重置输入参数
      this.rdoId = null;
      this.rdoBaseId = undefined;
      this.isRevOfRcdId = false;
      this.operationName = undefined;
      this.operationTime = undefined;
      // 重置表单数据
      this.resetFormData();
      // 重置表单之后
      this.resetPageForms();
    },
    /**
     * 给父级反馈并点击成编辑模式
     * @param result 返回的id
     */
    handleSaveEndBackView(result) {
      this.$emit('rowClick', {
        id: result.baseId,
        rdoBaseId: result.baseId,
        rdoId: result.id,
        name: result.name,
        version: result.version,
        isRevOfRcdId: result.isRevOfRcdId
      });
    },
    /** 刷新成功消息提示 */
    refreshParent(params) {
      if (!params) {
        params = {};
      }
      if (params.reset) {
        this.reset();
      }
      this.$emit("childRefresh", params);
    },
    checkPageForms() {
      return new Promise((resolve, reject) => {
        this.$refs.form
          .validate()
          .then(() => {
            resolve(true);
          })
          .catch((err) => {
            reject(err);
          });
      });
    },
    // 处理检验异常
    async checkError() {
      try {
        let valid = await this.checkPageForms();
        return valid;
      } catch (error) {
        return;
      }
    },
  },
});

export default GRdoPageEditBase;
