<template>
  <div @click="onSelectWrapperClick($event)" @mousedown="onSelectWrapperMousedown($event)">
  <a-select
    class="select"
    v-model="selectedValue"
    v-bind="getProps"
    :loading="loading"
    :allowClear="!loading"
    @change="change"
    @popupScroll="popupScroll"
    :open="open"
    :dropdown-style="{ maxHeight: '400px', overflow: 'auto' }"
  >
    <a-icon
      @click="clear"
      slot="clearIcon"
      type="close-circle"
      theme="filled"
    />
    <a-select-option
      v-for="item in listData"
      :key="item.value"
      :value="item.label"
      :option="item"
    >
      {{ item.label }}
    </a-select-option>
    <div slot="dropdownRender" slot-scope="menu">
      <div class="container" style="padding: 5px 0 5px 5px">
        <div @mousedown="onMousedownStopPropagation($event)"
              @click="onMousedownStopPropagation($event)"
              style="display: flex">
              <a-input
                style="width: calc(100% - 45px)"
                class="modeling-select-filter-input"
                type="text"
                placeholder="请输入"
                v-model="params.keyword"
              />
              <g-btn
                style="margin-left: 5px; width: 32px !important"
                class="modeling-select-filter-btn"
                type="primary"
                @callback="reloadChange()"
                icon="ri-search-line"
                text=""
              >
              </g-btn>
        </div>
      </div>
      <div @mousedown="onMousedownStopPropagation($event)"
              @click="onMousedownStopPropagation($event)">
      <v-nodes :vnodes="menu" />
      </div>
     
    </div>
    
  </a-select>
</div>
</template>

<script>
import { getDataAPI } from "./data";

/** @name 定义一些下拉框组件的默认值，为了更好的满足业务逻辑 **/
const DEFAULT_PROPS = {
  mode: "default", // 仅支持这两种模式: default/multiple
  placeholder: "请选择",
  showArrow: false, // 不使用默认的清除按钮，使用slot插入清除按钮，更好的控制清除事件
  filterOption: false, // 不使用默认的筛选规则，因为分页行为，筛选需要传递到接口层面去过滤
  defaultActiveFirstOption: false, // 是否默认高亮第一个选项, 默认是true
  optionLabelProp: "children", // 回填到选择框的 option 的属性值，默认是 Option 的子元素
};

export default {
  components: {
    VNodes: {
      functional: true,
      render: (_, ctx) => ctx.props.vnodes,
    },
  },
  props: {
    value: {
      type: [Number, String, Array],
      default: undefined,
    },
    /** @name 是否支持自定义输入的值，单选-直接输入即可，多选-需要回车操作 **/
    isCustomInput: {
      type: Boolean,
      default: false,
    },
    /** @name 加载方式，pagination/scroll **/
    loadType: {
      type: String,
      default: "pagination",
    },
    /** @name 分页参数 **/
    pagination: {
      type: Object,
      default: () => ({}),
    },
  },
  data() {
    return {
      loading: false,
      params: {
        pageNum: 1,
        pageSize: 5,
        keyword: "",
        selectId: "",
      },
      listData: [],
      total: 0,
      selectedValue: undefined,
      // 用于处理 search 事件默认最终会输出一次空的行为, 其主要会造成与用户删除的行为冲突
      open: false,
    };
  },
  computed: {
    getProps() {
      return {
        ...DEFAULT_PROPS,
        ...this.$attrs,
      };
    },
  },
  watch: {
    value: {
      handler: function (val) {
        this.selectedValue = this.value;
      },
      deep: true,
      immediate: true,
    },
    selectedValue() {
      this.$emit("input", this.selectedValue);
    },
  },
  created() {
    this.params = Object.assign(this.params, this.pagination);
    this.getData();
  },
  mounted() {
    // 挂载初始化
    this.componentMounted();
  },
  beforeUnmount() {
    // 释放
    this.componentDestroy();
  },
  methods: {
    /** @name 获取数据 **/
    reloadChange() {
      this.getData();
    },
     /** 筛选条件变更处理 */
     filterTextChangeSubSetup(remove = false) {
      
    },
    /** body鼠标点击事件 */
    bodyMousedown(remove = false) {
      // 卸载
      if (remove) {
        // 没有初始化，跳过卸载
        if (!this.bodyMousedownHandle) {
          return;
        }

        // 卸载
        document.body.removeEventListener('mousedown', this.bodyMousedownHandle);
        this.bodyMousedownHandle = null;
        return;
      }

      // 创建处理函数
      if (!this.bodyMousedownHandle) {
        this.bodyMousedownHandle = (event) => {
          // 没有展开
          if (!this.open) {
            return;
          }

          // 关闭
          this.setSelectOpen(false);
        };
      } else {
        // 初始化过了
        return;
      }

      // 注册
      document.body.addEventListener('mousedown', this.bodyMousedownHandle);
    },
    /** 包装器鼠标点击事件处理 */
    selectWrapperMousedown(remove = false) {
      // 卸载
      if (remove) {
        // 没有初始化，跳过卸载
        if (
          !this.selectWrapperMousedownHandle ||
          !this.selectWrapperRef ||
          !this.selectWrapperRef.getAttribute('data-inited')
        ) {
          return;
        }

        // 卸载
        this.selectWrapperRef?.removeEventListener('mousedown', this.selectWrapperMousedownHandle);
        this.selectWrapperMousedownHandle = null;
        return;
      }

      // 创建处理函数
      if (!this.selectWrapperMousedownHandle) {
        this.selectWrapperMousedownHandle = (e) => {
          if (this.open) {
            e.stopPropagation();
          }
        };
      }

      // 注册处理函数（避免重复注册）
      this.selectWrapperRef = this.$refs['selectWrapper'];
      if (this.selectWrapperRef && !this.selectWrapperRef.getAttribute('data-inited')) {
        this.selectWrapperRef.setAttribute('data-inited', 'true');
        this.selectWrapperRef.addEventListener('mousedown', this.selectWrapperMousedownHandle);
      }
    },
    /** 筛选鼠标点击事件处理 */
    selectFilterMousedown(remove = false) {
      // 卸载
      if (remove) {
        // 没有初始化，跳过卸载
        if (
          !this.selectFilterMousedownHandle ||
          !this.selectFilterRef ||
          !this.selectFilterRef.getAttribute('data-inited')
        ) {
          return;
        }

        // 卸载
        this.selectFilterRef?.removeEventListener('mousedown', this.selectFilterMousedownHandle);
        this.selectFilterMousedownHandle = null;
        return;
      }

      // 创建处理函数
      if (!this.selectFilterMousedownHandle) {
        this.selectFilterMousedownHandle = (e) => {
          if (this.open) {
            e.stopPropagation();
          }
        };
      }

      // 注册处理函数（避免重复注册）
      setTimeout(() => {
        this.selectFilterRef = this.$refs['selectFilter'];
        if (this.selectFilterRef && !this.selectFilterRef.getAttribute('data-inited')) {
          this.selectFilterRef.setAttribute('data-inited', 'true');
          this.selectFilterRef.addEventListener('mousedown', this.selectFilterMousedownHandle);
        }
      }, 50);
    },
      /** 组件挂载 */
      componentMounted() {
      // 注册
      this.bodyMousedown();
      this.selectWrapperMousedown();
      
      this.filterTextChangeSubSetup();
    },
    /** 组件释放 */
    componentDestroy() {
      // 释放
      this.bodyMousedown(false);
      this.selectWrapperMousedown(false);
      
      this.filterTextChangeSubSetup(false);
    },
    setSelectOpen(open) {
      this.open = open;
    },
     /** 下拉容器点击 */
     onSelectWrapperClick(e) {
      e.stopPropagation();
      this.setSelectOpen(!this.open);
    },
    /** 下拉容器鼠标点击 */
    onSelectWrapperMousedown(e) {
      // 展开状态停止冒泡
      if (this.open) {
        e.stopPropagation();
        e.stopImmediatePropagation();
        return;
      }
    },
     /** 鼠标点击事件设置冒泡 */
     onMousedownStopPropagation(e) {
      e.stopPropagation();
      e.stopImmediatePropagation();
      
      return;
    },
    /** @name 下拉列表滚动时的回调 **/
    popupScroll({ target }) {
      if (
        this.loading ||
        this.total <= this.listData.length ||
        this.loadType !== "scroll"
      )
        return;
      const isTouchBottom =
        target.scrollTop + target.offsetHeight + 10 > target.scrollHeight;
      if (isTouchBottom) {
        this.params.pageNum = this.params.pageNum + 1;
        this.getData(true);
      }
    },
    /** @name 键盘按下时回调 **/
    inputKeydown({ code, target }) {
      // 按下回车键
      if (code === "Enter" && this.isCustomInput) {
        const inputValue = target.value;
        this.selectedValue.push(inputValue);
        this.params.keyword = "";
      }
    },
    /** @name 搜索事件 **/
    search(value) {
      if (!this.getProps.showSearch) return;
      if (this.open) {
        const inputValue = value || undefined;
        if (this.isCustomInput) {
          // 单选-自定义输入值
          if (this.getProps.mode !== "multiple") {
            this.selectedValue = inputValue;
          }
        }
        this.params.keyword = inputValue;
        this.params.pageNum = 1;
        this.getData();
      }
    },
    /** @name 下拉框选择 **/
    change(selectedValue, options) {
      this.setSelectOpen(false);
      this.$emit("input", this.selectedValue);
      let selectedOption;
      if (Array.isArray(options)) {
        selectedOption = options.map((item) => item.data.attrs.option);
      } else {
        selectedOption = options.data.attrs.option;
      }
      // 找到和this.selectedValue相同的项
      if (this.selectedValue) {
        const index = this.listData.findIndex(
          (item) => item.label === this.selectedValue
        );
        // 把它添加到第一位 unshift
        this.listData.unshift(this.listData.splice(index, 1)[0]);
      }

      this.$emit("change", selectedValue, selectedOption);
    },
    /** @name 分页操作 **/
    paginationChange(page) {
      this.params.pageNum = page;
      this.getData();
    },
    /** @name 点击清除按钮 **/
    clear() {
      this.params = {
        pageNum: 1,
        pageSize: 5,
        keyword: "",
        ...this.pagination,
      };
      this.selectedValue = undefined;
      this.getData();
    },
    
    /** @name 重置下拉菜单数据 **/
    resetData() {
      this.params.pageNum = 1;
      this.params.keyword = "";
      this.getData();
    },
    /** @name 获取数据 **/
    getData(isScroll) {
      this.loading = true;
      this.params.selectId = this.selectedValue;
      getDataAPI(this.params)
        .then((res) => {
          
          this.listData = isScroll
            ? (this.listData = [...this.listData, ...res.rows])
            : res.rows;
          // 模拟搜索过滤 
          if(this.params.keyword) {
            this.listData = this.listData.filter((item) =>  item.label.indexOf(this.params.keyword) !== -1)
          }
          this.total = res.total;
          this.loading = false;
          // 处理 res.selectRow 如果已经添加了, 就不需要再添加
          if (this.selectedValue) {
            res.selectRow.forEach((item) => {
              if (!this.listData.includes(item)) {
                this.listData.unshift(item);
              }
            });
          }
        })
        .catch(() => {
          this.loading = false;
        });
    },
  },
};
</script>

<style lang="scss" scoped>
.select {
  width: 100%;
}
.divider {
  margin: 2px 0;
}
.paging-box {
  display: flex;
  justify-content: flex-end;
  box-sizing: border-box;
  padding: 4px 8px 8px 0;
}
.empty {
  text-align: center;
  font-size: 13px;
  color: #666;
}
:deep(.modeling-select-filter-btn .btn-box i){
 
    font-size: 16px !important;
  
}
</style>
