<template>
  <div class="g-blank">
    <div v-if="image" class="g-blank-image">
      <img :src="image" alt="Custom Image" />
    </div>
    <div v-else-if="text" class="g-blank-text">
      {{ text }}
    </div>
    <div v-else class="g-blank-default">
      <!-- 默认内容，例如一个占位符 -->
      <slot>
          <div>暂无数据</div>
      </slot>
    </div>
  </div>
</template>

<script>
export default {
  name: 'GBlank',
  props: {
    text: {
      type: String,
      default: ''
    },
    image: {
      type: String,
      default: ''
    }
  }
};
</script>

<style scoped>
.g-blank {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.g-blank-image img {
  max-width: 100%;
  max-height: 100%;
}

.g-blank-text,
.g-blank-default {
  color: #999;
  font-size: 14px;
}
</style>