<template>
  <div>
    <a-tree-select
      v-model="selectId"
      style="width: 100%"
      :dropdown-style="{ maxHeight: '400px', overflow: 'auto' }"
      :tree-data="listData"
      placeholder="请选择"
      @change="change"
    >
      <!-- title插槽 -->
      <template slot="title" slot-scope="dataRef">
        <div class="tree-box" @click="treeNodeClick(dataRef)">
          <span class="tree-title">
            {{ dataRef.label }}
          </span>
          <a-checkbox
            v-if="dataRef.isRevOfRcdId"
            :checked="dataRef.isRevOfRcdId"
            :disabled="true"
          />
        </div>
      </template>
    </a-tree-select>
    <!-- 版本号 -->
    <a-select
      v-model="rdoId"
      style="width: 50%; margin-top: 8px"
      placeholder="请选择版本号"
      class="mr-1"
      :disabled="useDefaultVersion || disabled"
      @change="selectVersion"
    >
      <a-select-option v-for="(item, i) in selectRdoList" :value="item.id">
        {{ item.revision }}
      </a-select-option>
    </a-select>
    <a-checkbox
      v-model:checked="useDefaultVersion"
      :disabled="disabled"
      @change="useDefaultVersionChange"
    />
  </div>
</template>

<script>
import { getDataAPI } from "./data";

export default {
  props: {
    value: {
      type: [Number, String, Array],
      default: undefined,
    },
    /** @name 是否支持自定义输入的值，单选-直接输入即可，多选-需要回车操作 **/
    isCustomInput: {
      type: Boolean,
      default: false,
    },
    /** @name 加载方式，pagination/scroll **/
    loadType: {
      type: String,
      default: "pagination",
    },
    /** @name 分页参数 **/
    pagination: {
      type: Object,
      default: () => ({}),
    },
    // 替换字段 replaceFields
    replaceFields: {
      type: Object,
      default: () => {
        return {
          key: "value",
          title: "label",
          value: "value",
          children: "children",
        };
      },
    },
    // 是否禁用
    disabled: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      loading: false,
      params: {
        pageNum: 1,
        pageSize: 5,
        keyword: "",
        selectId: "",
      },
      listData: [],
      total: 0,
      rdoBaseId: undefined,
      // 用于处理 search 事件默认最终会输出一次空的行为, 其主要会造成与用户删除的行为冲突
      open: false,
      rdoId: undefined,
      selectRdoList: [],
      useDefaultVersion: false,
      selectId: undefined,
    };
  },
  watch: {
    rdoBaseId(value) {
      this.$emit("input", value);
    },
  },
  created() {
    this.params = Object.assign(this.params, this.pagination);

    this.getData();
  },
  methods: {
    /** @name 获取数据 **/
    getData(isScroll) {
      this.loading = true;
      //   this.params.selectId = this.rdoBaseId;
      getDataAPI(this.params)
        .then((res) => {
          this.listData = isScroll
            ? (this.listData = [...this.listData, ...res.rows])
            : res.rows;
          this.total = res.total;
          this.loading = false;
          // 处理 res.selectRow 如果已经添加了, 就不需要再添加
          if (this.rdoBaseId) {
            res.selectRow.forEach((item) => {
              if (!this.listData.includes(item)) {
                this.listData.unshift(item);
              }
            });
          }
        })
        .catch(() => {
          this.loading = false;
        });
    },
    treeNodeClick(node) {
      if (node.isRevOfRcdId) {
        this.useDefaultVersion = true;
      } else {
        this.useDefaultVersion = false;
      }
      this.rdoId = undefined;

      if (node?.children) {
        this.rdoId = node?.revOfRcdId;
        this.useDefaultVersion = true;
        this.rdoBaseId = node.value;
        if (node?.children?.length) {
          this.selectRdoList = node?.children.map((i) => {
            return {
              id: i.id,
              revision: i.revision,
            };
          });
        }
      } else {
        this.rdoId = node.id;
        this.rdoBaseId = node.parentId;
        this.selectRdoList = this.listData
          .filter((i) => i.value == node.parentId)[0]
          .children.map((i) => {
            return {
              id: i.id,
              revision: i.revision,
            };
          });
      }
    },
    change(value) {},
    useDefaultVersionChange(e) {
      // 设置为默认版本，只选择rdo baseid
      console.log(this.rdoBaseId);
      if (e.target.checked) {
        const base = this.listData.filter((item) => {
          return item.value == this.rdoBaseId;
        });
        this.rdoId = base[0]?.revOfRcdId;
        this.selectId = [this.rdoBaseId];
        return;
      } else {
      }
    },
    selectVersion(rdoId) {
      console.log(rdoId);
    },
  },
};
</script>

<style lang="scss" scoped>
.tree-box {
  display: flex;
  align-items: center;
  .tree-title {
    flex: 1;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}

:deep(.ant-select-selection__rendered) {
  .ant-checkbox-wrapper {
    display: none;
  }
}
</style>
