<template>

    
  <a-sub-menu
    key="sub1"
    @titleClick="titleClick"
    v-if="filteredItems && filteredItems.length > 0"
  >
    <span slot="title"><a-icon type="mail" /><span>Navigation One</span></span>
    <SidebarItem
      :item="item1"
      :parent-path="getPath(parentPath, item.path)"
      :level="level + 1"
      v-for="(item1, index1) in filteredItems"
    />
  </a-sub-menu>
  
  <a-menu-item :key="getPath(parentPath, item?.path)" v-else>
    <a-icon
      :icon-name="item?.meta?.icon"
      v-if="level === 0"
      class="ml-1 mr-1 text-lg"
    />
    {{ item?.meta?.title }}
  </a-menu-item>
</template>

<script>
export default {
  name: "DataV2SliderBarItem",
  props: {
    item: { type: Object, default: () => {} },
    parentPath: { type: String, default: "" },
    level: { type: Number, default: 0 },
  },
  data() {
    return {};
  },
  computed: {
    filteredItems() {
      if (this.item?.children)
        return this.item?.children.filter((val) => !val.hidden);
      else return null;
    },
  },

  mounted() {},

  methods: {
    titleClick(e) {
      console.log(e);
    },
    getPath(parentPath, path) {
      if (parentPath) {
        return `${parentPath}/${path}`;
      } else {
        return path;
      }
    },
  },
};
</script>

<style lang="scss" scoped></style>
